import React from 'react'

import styles from './BitrixModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/bitrix-module-img.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const BitrixModule = (props) => {

    return (
        <div className={styles.BitrixModule}>
           <AdaptiveDevBlock>
           <h1>1С-Битрикс модуль</h1>

<p><b>Инструкция по установке</b></p>

<p>1. Скачайте&nbsp;<a href="/downloads/archives/bitrix/vepay.paymodule.zip">архив</a>, распакуйте и загрузите папку в&nbsp;<code>корень_сайта/bitrix/modules/</code></p>
<p>2. Перейдите в “Marketplace &gt; Установленные решения” и найдите “Платежная система Vepay”, после установите его.</p>
<p>3. Перейдите в “Настройки &gt; Настройки модулей”, выберите “Платежная система Vepay” и настройте модуль.<br/></p>
<AdaptiveImg image={img1} />
    <p>4. В настройках платежной системы установите адрес для обратного запроса на<br/>
    <code>http://ваш_сайт/vepay_processor/result.php?BX_HANDLER=VEPAY</code></p>







           </AdaptiveDevBlock>
        </div>
    )
}

export default BitrixModule