import React from 'react'

import styles from './Socials.module.scss'
import vk from '../../img/socials/vk.svg'
import facebook from '../../img/socials/facebook.svg'
import instagram from '../../img/socials/instagram.svg'
import telegram from '../../img/socials/telegram.svg'
import zennn from '../../img/socials/zennnn.svg'

const Socials = ({type='footer'}) => {

    const cls = [
        styles.Socials,
        type === 'footer'? styles.isFooter: ''
    ]

    return (
        <div className={cls.join(' ')}>

            <div className={styles.text}>
                <p>Присоединяйтесь</p>
            </div>

            <div  className={styles.imagesBlock}>
                <a href="https://vk.com/vepay" target='_blank' rel="noopener noreferrer">
                    <img src={vk} alt="vk" />
                </a>
                <a href="https://www.instagram.com/vepay.online/" target='_blank' rel="noopener noreferrer">
                    <img src={instagram} alt="instagram" />

                </a>
                <a href="https://www.facebook.com/vepay.online/" target='_blank' rel="noopener noreferrer">

                    <img src={facebook} alt="facebook" />
                </a>

                <a href="https://t.me/vepay_online" target='_blank' rel="noopener noreferrer">

                    <img src={telegram} alt="telegram" />
                </a>

                <a href="https://zen.yandex.ru/id/5ec6537bc5ff7a6742993582" target='_blank' rel="noopener noreferrer">
                    <img src={zennn} alt="zen" />

                </a>
            </div>

        </div>
    )
}

export default Socials