import React, { Fragment } from 'react'
import { useEffect } from 'react';
import { connect } from 'react-redux';
import Button from '../../../components/UI/Button/Button';
import { getNewsDetailsThunk } from '../../../redux/actions/blogActions';

import styles from './NewsDetails.module.scss'
import topImg from '../../../img/blog/blogTopImg.png'

const NewsDetails = ({details, getNewsDetailsThunk, history, match, ...props}) => {
    useEffect(() => {
        getNewsDetailsThunk(match.params.id)
        // eslint-disable-next-line
    }, [])
    
    return details && (
        <Fragment>
             <div className={styles.topImgBlock}>
                 <img className={styles.topImg} src={topImg} alt="topImg"/>
                 <h1>Блог компании</h1>
            </div>
            <div className={styles.NewsDetails}>
                <h2>{details.digest}</h2>
                <span>{details.date}</span>
                <div>
                    <img className={styles.image} src={details.image} alt={details.digest}/>
                </div>
                {/* <p dangerouslySetInnerHTML={{__html: details.content}}> */}
                <p>
                {details.content}
                </p>
                <div>
                    <Button
                        clickHander={() => history.push('/blog')}
                        text={'Назад к новостям'}
                    />
                </div>
            </div>
        </Fragment>
    )
}

const mapStateToProps = (state) => {
    return {
        details: state.blog.details,
    }
}


export default connect(mapStateToProps, {getNewsDetailsThunk})(NewsDetails)