import React from 'react'
import Button from '../../UI/Button/Button'

import styles from './BlackCard.module.scss'

const BlackCard = ({
  className, 
  header='BlackCard', 
  children, 
  buttonText='подключить', 
  clickHandler,
  contentStyles,
  h3
}) => {

  const cls = [
    styles.BlackCard,
    className
  ]

  const contentCls = [
    styles.content,
    contentStyles
  ]

    return (
      <div className={cls.join(' ')} onClick={clickHandler}>
            <div className={styles.cardColumn}>
              <div>
                <h1 className={styles.header}>{header}</h1>
                {h3 && (<h3 className={styles.h3}>{h3}</h3>)}
              </div>
              <div className={contentCls.join(' ')}>
                {children}
              </div>
              <div className={styles.button}>
                <Button
                  text={buttonText}
                  type={'green'}
                  clickHander={clickHandler}
                />
              </div>
           </div>

        </div>
    )
}

export default BlackCard