import React from 'react'

import styles from './MagentoModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/magento-module-img.jpg'
import img2 from '../../../img/developers/CMS-modules/magento-module-img2.jpg'
import img3 from '../../../img/developers/CMS-modules/magento-module-img3.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const MagentoModule = (props) => {

    return (
        <div className={styles.MagentoModule}>
            <AdaptiveDevBlock>
                <h1>Magento 2 модуль</h1>
                <h3>
                Инструкция по установке
                </h3>
                <p>
                1. Скачайте архив и распакуйте его в корень сайта
                </p>
                <p>
                2. Перейдите в корень сайта и в консоли пропишите
                </p>
                <p>
                <code>
php bin/magento module:enable Magento_VepayPaymentGateway <br/>
php bin/magento setup:upgrade<br/>
php bin/magento setup:di:compile
</code>
                </p>
                <p>
                3. Перейдите в "Stores {'>'} Configuration", выберите "Платежная система Vepay" и настройте модуль.
                </p>
                <AdaptiveImg image={img1} />
                <p>
                4. В разделе "Sales" выберите "Payment methods"
                </p>
                <AdaptiveImg image={img2} />
                <p>
                5. Выполните найстройки и нажмите "Save Config"
                </p>
                <AdaptiveImg image={img3} />
                








            </AdaptiveDevBlock>
        </div>
    )
}

export default MagentoModule