import React from 'react'
import BlackTop from '../../components/Blocks/BlackTop/BlackTop'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import Button from '../../components/UI/Button/Button'

import styles from './Feedback.module.scss'

const Feedback = (props) => {

    return (
        <div className={styles.Feedback}>
            <BlackTop/>
            <MaxWidthBlock>
                 <div  className={styles.innerBlock}>


                    <h1>Обратная связь</h1>
                    <h2>
                    Вы можете обратиться в наш офис
                    </h2>
                    <p>
                    105064, Москва, Нижний Сусальный переулок, 5с18
                    </p>
                    <h3>
                    Либо связаться по телефону или почте:
                    </h3>
                    <span>Электронная почта</span>
                    <h3>
                    info@vepay.online
                    </h3>

                    <span>Контактный телефон</span>
                    <a href="tel:+7 499 954-84-95"><h3>+7 499 954-84-95</h3></a>
                    <h3>Либо написать сообщение на сайте:</h3>
                    <Button
                    text={'Написать сообщение'}
                    />
                </div>
            </MaxWidthBlock>

        </div>
    )
}

export default Feedback