import React from 'react'

import styles from './Header.module.scss'
import VepayLogo from '../VepayLogo/VepayLogo'
import NavMenu from '../NavMenu/NavMenu'
import OpenMenuButton from '../UI/OpenMenuButton/OpenMenuButton'
import AccButton from '../UI/AccButton/AccButton'


const Header = (props) => {

   


    return (
        <nav className={styles.Header}>
            <VepayLogo className={styles.logo}/>
            <NavMenu isHeader={true} />
            <AccButton className={styles.accButton}/>
            <OpenMenuButton/>
        </nav>
    )
}

export default Header