import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import {closeMenu} from '../../../redux/actions/layoutActions'
import styles from './NavLinkPurple.module.scss'

const NavLinkPurple = ({closeMenu, className, ...props}) => {

    const cls = [
        styles.NavLinkPurple,
        className
    ]

    return (
        <NavLink 
            className={cls.join(' ')}
            {...props} 
            activeStyle={{
                fontWeight: "bold",
                color: "#A31FED"
            }}
            onClick={closeMenu}
        >
            {props.children}
        </NavLink>
    )
}



export default connect(null, {closeMenu})(NavLinkPurple)