import React from 'react'

import styles from './CScartModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/cscart-module-img.jpg'
import img2 from '../../../img/developers/CMS-modules/cscart-module-img2.jpg'
import img3 from '../../../img/developers/CMS-modules/cscart-module-img3.jpg'
import img4 from '../../../img/developers/CMS-modules/cscart-module-img4.jpg'
import img5 from '../../../img/developers/CMS-modules/cscart-module-img5.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const CScartModule = (props) => {

    return (
        <div className={styles.CScartModule}>
            <AdaptiveDevBlock>
                <h1>CS-Cart модуль</h1>
                <h3>
                Инструкция по установке
                </h3>
                <p>
                1. Скачайте архив с модулем
                </p>
                <p>
                2. Скопируйте файлы из архива в корень сайта
                </p>
                <p>
                3. Перейдите в Модули {'>'} Управление модулями
                </p>
                <AdaptiveImg image={img1} />
                <p>
                4. Найдите Vepay и нажмите установить
                </p>
                <AdaptiveImg image={img2} />
                <p>
                5. Перейдите в Администрирование {'>'} Способы оплаты
                </p>
                <AdaptiveImg image={img3} />
                <p>
                6. Нажмите Добавить спросо оплаты и в поле "Процессор" наберите Vepay
                </p>
                <AdaptiveImg image={img4} />
                <p>
                7. Перейдите в настройки и заполните поля
                </p>
                <p>
                Заполните Merch Id, Merch Token, Callback Token из вашего личного кабинета. Callback Url укажите в настройках личного кабинета. 
                </p>
                <AdaptiveImg image={img5} />
                <p>
                8. После заполнения настроек нажмите "Создать"
                </p>











            </AdaptiveDevBlock>
        </div>
    )
}

export default CScartModule