import React, { Fragment, useState } from 'react'
import Button from '../../UI/Button/Button'
import paymentTerminal from '../../../img/legal-entity-main/infoBlockImg1.png'
import integrationImg from '../../../img/legal-entity-main/infoBlockImg2.png'
import widgetImg from '../../../img/legal-entity-main/infoBlockImg3.png'


import styles from './InfoBlock.module.scss'

const InfoBlock = ({ history }) => {
    const [navItem, setNavItem] = useState('technology')

    const technologyCls = [styles.infoNavItem]
    const integrationCls = [styles.infoNavItem]
    const widgetCls = [styles.infoNavItem]

    navItem === 'technology' ? technologyCls.push(styles.active)
        : navItem === 'integration' ? integrationCls.push(styles.active)
            : widgetCls.push(styles.active)

    const technology = (
        <Fragment>
            <h1>Технологии оплаты и приема платежей</h1>
            <div className={styles.paragraphs}>
                <p>
                    Современные методы платежей для всех видов онлайн-бизнеса. Оплата товара или услуг любым удобным способом.
                        </p>
                <p>Технологии оплаты: Банковские карты, Apple Pay, Google Pay, Samsung Pay, мобильная коммерция, электронные кошельки и многое другое.</p>

            </div>
        </Fragment>
    )

    const integration = (
        <Fragment>
            <h1>Легкая интеграция </h1>
           <div className={styles.paragraphs}>
            <p>
                    Различные варианты подключения, понятная документация, техническая поддержка и личный менеджер.
                </p>
                <p>
                    Интеграция системы через подробный и понятный API. Готовые SDK-модули с возможностью приема платежей в нативных приложениях iOS, Android. Модули для CMS. Набор инструментов для Разработчиков.
                </p>
           </div>

        </Fragment>
    )

    const widget = (
        <Fragment>
            <h1>Платежный виджет</h1>
            <div className={styles.paragraphs}>
                <p>
                    Интуитивно понятная платежная форма для мгновенной оплаты на сайте без перехода на дополнительный платежный шлюз.
                </p>
                <p>
                    Кастомизация платежной страницы в фирменном стиле компании партнёра. Возможность настройки виджета от простой до полноценного маркетплейса. Соответствует требованиям PCI DSS
                </p>
            </div>
        </Fragment>
    )

    const navMenuContent = { technology, integration, widget }

    const navItemImages = {
        technology: paymentTerminal,
        integration: integrationImg,
        widget: widgetImg
    }

    const buttonsText = {
        technology: 'Подробнее о технологиях',
        integration: 'О методах интеграции',
        widget: 'О платежном виджете'
    }
    const buttonLinks = {
        technology: '/developers',
        integration: '/developers/connection',
        widget: '/developers/payment-widget'
    }

    return (
        <div className={styles.InfoBlock}>
            <div className={styles.relativeBlock}>
                <img src={navItemImages[navItem]} className={styles.image} alt="infoImg"/>

                <nav className={styles.infoNav}>
                    <div
                        className={technologyCls.join(' ')}
                        onClick={() => setNavItem('technology')}
                    ></div>
                    <div
                        className={integrationCls.join(' ')}
                        onClick={() => setNavItem('integration')}
                    ></div>
                    <div
                        className={widgetCls.join(' ')}
                        onClick={() => setNavItem('widget')}
                    ></div>

                </nav>
            </div>
            <div className={styles.infoBlockText}>

                {navMenuContent[navItem]}

                <div className={styles.btnBlock}>
                    <Button
                        text={buttonsText[navItem]}
                        type='green'
                        className={styles.btn}
                        clickHander={() => history.push(buttonLinks[navItem])}
                    />
                </div>

            </div>


        </div>
    )
}

export default InfoBlock