import React from 'react'
import BlackTop from '../../components/Blocks/BlackTop/BlackTop'

import classes from './NotFound.module.scss'

import notFoundImg from '../../img/not-found/notFoundImg.png'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

const NotFound = (props) => {

    return (
        <div className={classes.NotFound}>
            <BlackTop/>
            <ColouredBGBlock>
                <MaxWidthBlock>
                    <img className={classes.notFoundImg} src={notFoundImg} alt="notFoundImg"/>
                    <center><h1>Страница не найдена</h1></center>
                    <center> <h2>Попробуйте найти в меню то, что Вам нужно</h2></center>
                </MaxWidthBlock>
            </ColouredBGBlock>
            
        </div>
    )
}

export default NotFound