import React from 'react'
import { connect } from 'react-redux'
import VepayLogo from '../VepayLogo/VepayLogo'

import styles from './SideMenu.module.scss'
import CloseButton from '../UI/CloseButton/CloseButton'
import { closeMenu } from '../../redux/actions/layoutActions'
import NavMenu from '../NavMenu/NavMenu'
import ContactInfo from '../ContactInfo/ContactInfo'
import Socials from '../Socials/Socials'
import AccButton from '../UI/AccButton/AccButton'
import DropDown from '../UI/DropDown/DropDown'

const SideMenu = ({isOpened, closeMenu}) => {

    const cls = [
        styles.SideMenu, 
    ]

    if (isOpened) {
        cls.push(styles.open)
    }

    const aboutCompany = [
        {
            title: 'О нас', 
            link: `/about-us`
        },
        {
            title: 'Контакты', 
            link: `/contacts`
        },
        {
            title: 'Блог', 
            link: `/blog`
        },
        {
            title: 'Маркетинговые материалы', 
            link: `/marketing`
        },
    ]

    return (
        <div className={cls.join(' ')}>
            <CloseButton closeMenu={closeMenu} />
            <VepayLogo type='sidemenu'/>
            <DropDown
                title='О компании'
                links={aboutCompany}
                titleClass={styles.link}
                className={styles.dropdown}
            />
            <NavMenu/>
            <AccButton className={styles.accButton} />
            <ContactInfo type='sidemenu'/>
            <Socials type='sidemenu'/>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        isOpened: state.layout.menuIsOpened
    }
}



export default connect(mapStateToProps, {closeMenu})(SideMenu)