import React from 'react'

import styles from './CrowdFunding.module.scss'
import topImg from '../../../img/crowd-funding/crowdTopImg1.png'
import topImg2 from '../../../img/crowd-funding/crowdTopImg2.png'
import cfSolutionImg1 from '../../../img/crowd-funding/cfSolutionImg1.svg'
import cfSolutionImg2 from '../../../img/crowd-funding/cfSolutionImg2.svg'
import cfSolutionImg3 from '../../../img/crowd-funding/cfSolutionImg3.svg'
import cfSolutionImg4 from '../../../img/crowd-funding/cfSolutionImg4.svg'
import cfSolutionImg5 from '../../../img/crowd-funding/cfSolutionImg5.svg'
import cfSolutionImg6 from '../../../img/crowd-funding/cfSolutionImg6.svg'
import cfSolutionImg7 from '../../../img/crowd-funding/cfSolutionImg7.svg'
import cfSolutionImg8 from '../../../img/crowd-funding/cfSolutionImg8.svg'
import cfSolutionImg9 from '../../../img/crowd-funding/cfSolutionImg9.svg'
import cfSolutionImg10 from '../../../img/crowd-funding/cfSolutionImg10.svg'
import TopBlockWithImageNEW from '../../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import SolutionsCard from '../../../components/Cards/SolutionsCard/SolutionsCard'

const CrowdFunding = (props) => {

  const cfSolutionsList = [
    {
      image: cfSolutionImg1,
      text: 'Открытие номинального счета в банке-партнёре;'
    },
    {
      image: cfSolutionImg2,
      text: 'Осуществление движения денежных средств между номинальными и расчётными счетами;'
    },
    {
      image: cfSolutionImg3,
      text: 'Предоставление доступа к API в тестовой среде;'
    },
    {
      image: cfSolutionImg4,
      text: 'Пополнение номинального счета с помощью банковской карты и расчетного счета в режиме онлайн;'
    },
    {
      image: cfSolutionImg5,
      text: 'Помощь в интеграции площадки с API;'
    },
    {
      image: cfSolutionImg6,
      text: 'Отправку и возврат денежных средств;'
    },
    {
      image: cfSolutionImg7,
      text: 'Автоматизированную проверку документов и добавление бенефициаров к номинальному счету;'
    },
    {
      image: cfSolutionImg8,
      text: 'Распределение денежных средств;'
    },
    {
      image: cfSolutionImg9,
      text: 'Подробную аналитику и учет движения денежных средств инвесторов и заёмщиков;'
    },
    {
      image: cfSolutionImg10,
      text: 'Оказание технической поддержки 24/7.'
    },
  ]

    return (
        <div className={styles.CrowdFunding}>

            <TopBlockWithImageNEW
              topBlockImg={topImg}
              topBlockImgNarrow={topImg2}
              topBlockItemText1='Для краудфандинговых платформ'
              topBlockItemText2='Уникальное решение в соответствии с ФЗ от 02.08.2019 №259-ФЗ «О привлечении инвестиций с использованием инвестиционных платформ» для краудфандинговых платформ для создания и реализации бизнес проектов.'
            />

            <ColouredBGBlock>
              <MaxWidthBlock>
                <h1 className={styles.cfTitle}>Решение в себя включает:</h1>
                <div className={styles.solutionsBlock}>
                  {
                    cfSolutionsList.map(item => <SolutionsCard key={item.text} {...item} />)
                  }
                </div>
              </MaxWidthBlock>
            </ColouredBGBlock>

        </div>
    )
}

export default CrowdFunding