import React from 'react'

import styles from './NewsDigest.module.scss'

const NewsDigest = ({item, history}) => {


  const onClick = () => {
    history.push(`/blog/${item.id}`)
  }

    return (
        <div onClick={onClick} className={styles.NewsDigest}>
            <div>
              <img className={styles.image}  src={item.image} alt={item.digest}/>
            </div>
            <div className={styles.newsFooter}>
              <h2>{item.digest}</h2>
              <span className={styles.date} >{item.date}</span>
            </div>
        </div>
    )
}

export default NewsDigest