import React, { useState } from 'react'

import styles from './Partners.module.scss'
import topImg from '../../img/partners/partnersTopImg.png'
import topImg2 from '../../img/partners/partnersTopImg2.png'
import talkingPeople from '../../img/partners/talking-people.png'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import AdaptiveParagraphs from '../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'
import InputRange from '../../components/UI/InputRange/InputRange'
import Button from '../../components/UI/Button/Button'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

const Partners = (props) => {

    const rangeParams = {
        STEP: 0.1, MIN: 0.5, MAX: 15, units: 'млн'
    }

    const [values, setValues] = useState([7.7])

    const topBlockItemText2 = (
        <span>Для рекламных и маркетинговых агентств, для digital-компаний и финансовых организаций. <br/><br/> Рекомендуйте VEPAY своим партнёрам и клиентам. Зарабатывайте на рекомендациях!</span>
    )

    const advantagesList = [
        {
            title: 'Легко ',
            text: 'Быстрая регистрация без лишних документов'
        },
        {
            title: 'Прозрачно ',
            text: 'Полная аналитика по привлеченным Клиентам и вознаграждению'
        },
        {
            title: 'Удобно ',
            text: 'Удобный личный кабинет, рекламные материалы, поддержка личного менеджера'
        },
        {
            title: 'Выгодно ',
            text: 'Выгодные условия работы. Различные варианты выплат вознаграждения'
        },
        {
            title: 'Безопасно ',
            text: 'Все данные зашифрованы'
        },
    ]



    return (
        <div className={styles.Partners}>

           <TopBlockWithImageNEW
               topBlockImg={topImg}
               topBlockImgNarrow={topImg2}
               topBlockItemText1='Партнерская программа'
               topBlockItemText2={topBlockItemText2}
               buttonText='стать партнером'
           />

           <ColouredBGBlock>
            <MaxWidthBlock>

               <AdaptiveFlexBlock className={styles.talkingPeopleBlock}>
                   <AdaptiveColumn className={styles.talkingPeopleImg}>
                       <img src={talkingPeople} alt="talkingPeople"/>
                   </AdaptiveColumn>
                   <AdaptiveColumn>
                       <h2 className={styles.talkText}>
                       VEPAY — это технологичный процессинговый центр. 
                       </h2>
                       <h2 className={styles.talkText}>
                       Мы высоко ценим наших Партнеров и клиентов и выстраиваем эффективное и долгосрочное сотрудничество.
                       </h2>
                   </AdaptiveColumn>
               </AdaptiveFlexBlock>

               <div className={styles.inputRangeBlock}>
                    <div className={styles.rangeHeader}>
                        <h1>Величина вознаграждения партнёра</h1>
                        <p>
                        Зарабатывайте с каждого приведенного клиента до 0,2% от его оборота в месяц.
                        </p>
                    </div>
                   <InputRange 
                    values={values} 
                    setValues={setValues}
                    bgColor={'#03EE65'}
                    {...rangeParams}
                   />
                   <p className={styles.turnOver}>
                   оборот клиента
                   </p>
                   <div className={styles.resultBlock}>
                        <p>
                            <span className={styles.result}>{(values * 1000000 / 100 / 5).toFixed().toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ')} ₽ </span>
                            <br/>
                            <span> Ваш доход в месяц с одного клиента</span>
                        </p>
                        <div className={styles.btnWrapper}>
                            <Button
                                text='стать партнером'
                                type='green'
                            />
                        </div>
                   </div>
               </div>


                <AdaptiveParagraphs className={styles.advantagesList}>
                    {
                        advantagesList.map(item => <ParagraphWithTitle {...item} key={item.title} />)
                    }
                </AdaptiveParagraphs>

            </MaxWidthBlock>
           </ColouredBGBlock>

        </div>
    )
}

export default Partners