import React from 'react'

import styles from './Payouts.module.scss'
import topImg from '../../img/payouts/payoutsTopImg.png'
import topImgNarrow from '../../img/payouts/payoutsTopImg2.png'
import payout1 from '../../img/payouts/payout1.svg'
import payout2 from '../../img/payouts/payout2.svg'
import payout3 from '../../img/payouts/payout3.svg'
import payout4 from '../../img/payouts/payout4.svg'
import payout5 from '../../img/payouts/payout5.svg'
import payout6 from '../../img/payouts/payout6.svg'
import payout7 from '../../img/payouts/payout7.svg'
import payout8 from '../../img/payouts/payout8.svg'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

const Payouts = (props) => {

    const paragraphsList = [
        {
            header: 'Мобильная коммерция ',
            text: 'Со счетов операторов сотовой связи: МТС, Билайн, Мегафон, Теле2.',
            image: payout1
        },
        {
            header: 'Двухстадийная оплата ',
            text: 'Списание денежных средств с карты с определенной периодичностью.',
            image: payout2
        },
        {
            header: 'Оплата по ссылкам ',
            text: 'Автоматическое уведомление клиентов об оплате (e-mail, sms, whatsapp, telegram, viber).',
            image: payout3
        },
        {
            header: 'Электронные кошельки ',
            text: 'Виртуальный кошелек для хранения электронных денежных средств для оплаты покупок в Интернете.',
            image: payout4
        },
        {
            header: 'Оплата через QR-код ',
            text: 'Проведение платежа по заранее сформированному QR-коду с зшифрованными данными.',
            image: payout5
        },
        {
            header: 'AFT ',
            text: 'Операции по погашению с карт клиентов кредитов и займов с зачислением на счет организации.',
            image: payout6
        },
        {
            header: 'Рекуррентные платежи ',
            text: 'Автоматические платежи по подписке позволяют регулярно списывать средства с банковской карты без ввода реквизитов.',
            image: payout7
        },
        {
            header: 'Токенизация ',
            text: 'Возможность проведения выплат без необходимости ввода и хранения карточных данных на стороне интернет-магазина.',
            image: payout8
        },
    ]

    return (
        <div className={styles.Payouts}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImgNarrow}
                topBlockItemText1={'Массовые и единичные выплаты в 1 клик'}
                topBlockItemText2={'Онлайн-перевод денежных средств с расчетного счета на банковскую карту любого банка, банковские счета, мобильные телефоны и электронные кошельки.'}
            
            />

            <ColouredBGBlock>
                <MaxWidthBlock>
                    <div className={styles.payoutsBlock}>
                        {
                            paragraphsList.map((item, i) => <SolutionsCard {...item} key={i}/>)
                        }
                    </div>
                    
                </MaxWidthBlock>
            </ColouredBGBlock>
        </div>
    )
}

export default Payouts