import { CLOSE_CONNECT_MODAL, OPEN_CONNECT_MODAL } from "../actions/actionTypes";

const initialState = {
    isOpen: false,
    error: null,

}


export default function connectModalReducer(state = initialState, action) {
    switch (action.type) {
        case OPEN_CONNECT_MODAL:
          return {
            ...state, isOpen: true
          }
        case CLOSE_CONNECT_MODAL:
          return {
            ...state, isOpen: false
          }
        default:
            return state;
    }

}