import React from 'react'
import Socials from '../Socials/Socials'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'

import styles from './Footer.module.scss'
import visaMaster from '../../img/footer/visa-master.svg'

const Footer = (props) => {

    return (
        <div className={styles.Footer}>
            <div className={styles.footerNavbar}>
                <ul  className={styles.list}>
                    <li className={styles.item}>
                        <NavLinkPurple to="/contacts" className={styles.link}>Связаться с нами</NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/partners" className={styles.link}>Партнёрам</NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/system-rules" className={styles.link}>Юридические соглашения</NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/help" className={styles.link}>Центр информации</NavLinkPurple>
                    </li>
                </ul>
                <Socials/>
            </div>
            <hr/>
            <div className={styles.footerNavbar2}>
                <ul  className={styles.list}>
                    <li className={styles.item}>
                        <NavLinkPurple to="/about-us" className={styles.link}>О нас</NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/developers" className={styles.link}>Разработчикам</NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/vacancies" className={styles.link}>Вакансии</NavLinkPurple>
                    </li>
                </ul>
                <div className={styles.visaMasterBlock}>
                    <div>
                        <span>© 1999–2020</span>
                    </div>
                    <div>
                        <img src={visaMaster} alt="visaMaster"/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer