// import axios from 'axios'
import { mockNews } from '../../utils/mock-news'


import { GET_NEWS_FAILURE, GET_NEWS_SUCCESS, NEWS_LOADING,
  GET_NEWS_DETAILS
 } from "./actionTypes"


export const getNewsThunk = () => {
  return async (dispatch) => {
    dispatch(setNewsLoading(true))
    try {
      dispatch(setNews(mockNews))
      // const res = await axios.get(`/api/news`)
      // if (res.status === 200 && res.data.result) {
        // dispatch(setNewsFailure(null))
      //   dispatch(setNews(res.data.result.news))
      // } else {
      //   dispatch(setNewsFailure(res.data.errors))
      // }
    } catch (error) {
      dispatch(setNewsFailure(['Сервер недоступен']))
      console.log(error);
    } finally {
     dispatch(setNewsLoading(false))
    }
  }
}

export const getNewsDetailsThunk = (id) => {
  return async (dispatch) => {
    dispatch(setNewsLoading(true))
    try {
      dispatch(setNewsDetails(mockNews.find((news) => news.id === +id)))
      console.log(mockNews.find((news) => news.id === +id));
      // const res = await axios.get(`/api/news/${id}`)
      // if (res.status === 200 && res.data.result) {
         // dispatch(setNewsFailure(null))
      //   dispatch(setNewsDetails(res.data.result.news))
      // } else {
      //   dispatch(setNewsFailure(res.data.errors))
      // }
    } catch (error) {
      
    } finally {
      dispatch(setNewsLoading(false))
    }
  }
}


export const setNewsLoading = (status) => {
  return {
    type: NEWS_LOADING,
    status
  }
}

export const setNewsFailure = (errors) => {
  return {
    type: GET_NEWS_FAILURE,
    errors
  }
}

export const setNews = (news) => {
  return {
    type: GET_NEWS_SUCCESS,
    news
  }
}
export const setNewsDetails = (details) => {
  return {
    type: GET_NEWS_DETAILS,
    details
  }
}