import React, { useEffect, useRef, useState } from 'react'

import styles from './ReceivingPayments.module.scss'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import topImg from '../../img/receiving-payments/paymentReceivingTopImg.png'
import topImgNarrow from '../../img/receiving-payments/paymentReceivingTopImg2.png'
import solutionImg1 from '../../img/receiving-payments/method1.svg'
import solutionImg2 from '../../img/receiving-payments/method2.svg'
import solutionImg3 from '../../img/receiving-payments/method3.svg'
import solutionImg4 from '../../img/receiving-payments/method4.svg'
import solutionImg5 from '../../img/receiving-payments/method5.svg'
import solutionImg6 from '../../img/receiving-payments/method6.svg'
import solutionImg7 from '../../img/receiving-payments/method7.svg'
import solutionImg8 from '../../img/receiving-payments/method8.svg'
import solutionImg9 from '../../img/receiving-payments/method9.svg'
import widgetImg1 from '../../img/receiving-payments/widgetImg1.png'
import widgetImg2 from '../../img/receiving-payments/widgetImg2.png'
import widgetImg3 from '../../img/receiving-payments/widgetImg3.png'
import widgetImg4 from '../../img/receiving-payments/widgetImg4.png'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import TextWithCircle from '../../components/UI/TextWithCircle/TextWithCircle'
import HowToPayStep from '../../components/HowToPayStep/HowToPayStep'

const ReceivingPayments = (props) => {
    const [stepNumber, setStepNumber] = useState(0)
    const [fadeIn, setFadeIn] = useState(false)
    const imageRef = useRef()

    useEffect(() => {
        setFadeIn(true)
        setTimeout(() => {
            setFadeIn(false)
        }, 1000)
    }, [stepNumber])

    const widgetImgCls = [
        styles.widgetImg,
        fadeIn? styles.fadeIn: ''

    ]

    const tickedTextList1 = [
        'Начало работы в течение 3 дней с момента подписания договора; ',
        'Работа с крупнейшими Банками-эквайерами России; ',
        'Собственная Anti-fraud система, блокирующая мошеннические операции;',
       
    ]

    const tickedTextList2 = [
        'VEPAY соответствует требованиям безопасности PCI DSS;',
        'Сопровождение личного клиентского менеджера на всех этапах сотрудничества; ',
        'Техническая поддержка 24/7',
    ]

    const solutionsList = [
        {
            header: 'Оплата по счету',
            text: 'Автоматическое формирование счета по заранее заданным реквизитам. Возможность печати файла или сохранения на устройстве.',
            image: solutionImg1
        },
        {
            header: 'QR-код',
            text: 'Оплата заказа/коммунальных услуг/проезда на транспорте/чаевых по сформированному QR-коду. Смартфон считывает QR-код, и клиент получает все данные для оплаты или перевода денежных средств, не тратя время.',
            image: solutionImg2
        },
        {
            header: 'Электронные кошельки',
            text: 'Электронный кошелек — это аналог банковской карты. Безопасный способ оплаты в интернет-магазинах. Для удобства использования кошелек привязывается к номеру мобильного телефона.',
            image: solutionImg3
        },
        {
            header: 'WeChat',
            text: 'Быстрый и удобный способ оплаты в одно касание. Используется для проведения расчетов с покупателями из Китая. Оплата через WeChat происходит по сгенерированным QR-кодам.',
            image: solutionImg4
        },
        {
            header: 'Мобильная коммерция',
            text: 'Позволяет совершать платежные операции со счета мобильного телефона абонента. Списание денежных средств происходит после акцептования платежа абонентом.',
            image: solutionImg5
        },
        {
            header: 'Оплата по ссылкам',
            text: 'Уведомление клиентов об оплате в режиме онлайн. Легкая организация рассылки из Личного кабинета. Возможность настройки уведомлений для клиентов.',
            image: solutionImg6
        },
        {
            header: 'Банковские карты',
            text: 'Осуществление платежей банковскими картами (Mastercard, Visa, МИР). Безопасность проведения платежа гарантируется сертификатом PCI DSS level 1.',
            image: solutionImg7
        },
        {
            header: 'Автоплатеж',
            text: 'Способ автоматического проведения платежей с банковской карты. Перевод денежных средств на оплату с заданными параметрами в необходимую дату и время осуществления платежа.',
            image: solutionImg8
        },
        {
            header: 'Сервисы бесконтактной оплаты',
            text: 'Технология оплаты покупок, которая не требует ввода PIN-кода и непосредственного контакта с терминалом.',
            image: solutionImg9
        },
    ]
    const steps = [
        {
            text: 'Пользователь подтверждает готовность оплатить услугу, переходит на страницу платежной формы и выбирает метод оплаты.'
        },
        {
            text: 'В открывшемся виджете для оплаты, пользователь указывает реквизиты карты, нажимает кнопку "оплатить"'
        },
        {
            text: 'Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты. Вводит код подтверждения из sms, нажимает кнопку "отправить"'
        },
        {
            text: 'Пользователь получает информацию о результате оплаты.'
        },
    ]
    const stepImages = [
        widgetImg1,
        widgetImg2,
        widgetImg3,
        widgetImg4
    ]

    return (
        <div className={styles.ReceivingPayments}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImgNarrow}
                topBlockItemText1={'Готовые решения для приема платежей'}
                topBlockItemText2={'Оплата на сайте, в мобильном приложении или торговом зале. В любом месте: в интернет-магазине, чате или приложении. В любое время. Любым способом: банковской картой, с помощью мобильного телефона и даже в кредит.'}
            />

            <ColouredBGBlock>
                <MaxWidthBlock>

                   <center> <h1>Преимущества VEPAY:</h1></center>

                    <div className={styles.tickedBlocks}>
                        <div className={styles.tickedBlock1}>
                            {
                                tickedTextList1.map(text => <TextWithCircle className={styles.tickedText} text={text} type={'violet'} key={text}/>)
                            }
                        </div>
                        <div className={styles.tickedBlock2}>
                            {
                                tickedTextList2.map(text => <TextWithCircle className={styles.tickedText} text={text} type={'violet'} key={text}/>)
                            }
                        </div>
                    </div>

                    

                </MaxWidthBlock>
                <div className={styles.solutionsWrap}>
                    <h1 className={styles.solutionsHeader}>Способы платежей</h1>
                    <div className={styles.solutionsBlock}>
                        {
                            solutionsList.map((item, i) => <SolutionsCard {...item} key={i}/>)
                        }
                    </div>
                </div>

                <div className={styles.howToPayBlock}>
                    <center><h1>Как оплатить через VEPAY</h1></center>

                    <div className={styles.howToPayBlockContent}>
                        <div className={styles.contentItem}>
                            <img ref={imageRef} className={widgetImgCls.join(' ')} src={stepImages[stepNumber]} alt="widgetImg"/>
                        </div>
                        <div className={styles.contentItem}>
                            {
                                steps.map((item, i) => {
                                    return (
                                        <HowToPayStep
                                            key={i}
                                            {...item}
                                            number={i + 1}
                                            active={i === stepNumber}
                                            clickHander={() => {
                                                setStepNumber(i)
                                                if (document.body.clientWidth < 900) {
                                                    imageRef.current.scrollIntoView({
                                                        behavior: "smooth",
                                                        });
                                                }
                                            }}
                                        />
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </ColouredBGBlock>

            


            
        </div>
    )
}

export default ReceivingPayments