import React from 'react'
import BlackTop from '../../components/Blocks/BlackTop/BlackTop'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

import styles from './Vacancies.module.scss'

const Vacancies = ({className, ...props}) => {

    const cls = [
        styles.Vacancies,
        className
    ]

    return (
        <div className={cls.join(' ')}>
            <BlackTop/>
            <ColouredBGBlock>
              <MaxWidthBlock>

                <div className={styles.contentBlock}>
                  <center><h1>Вакансии</h1></center>
                </div>




              </MaxWidthBlock>
            </ColouredBGBlock>
        </div>
    )
}

export default Vacancies