import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './FAQ.module.scss'

const FAQ = (props) => {

    return (
        <div className={styles.FAQ}>
           <AdaptiveDevBlock>
           <h1>FAQ</h1>
<p>Для обращения в службу технической поддержки vepay по адресу support@vepay.online, предоставьте vepayid или extid транзакций, и опишите вашу проблему.</p>
<h2>Завис платеж<strong>.</strong></h2>
<p>В большинстве случаев, платежи проводятся в день поступления заявки, но бывают и исключения. Время рассмотрения платежей Расчетным центром банка до 3х рабочих дней с 9-18ч. Если платеж пришел позже, то он может быть перенесен на следующий день. Также задержка может быть обусловлена техническими проблемами на стороне банка или Расчетного центра.</p>
<p><strong>Что делать</strong>: напишите письмо на support@vepay.online с описанием проблемы и мы уточним статус платежа.</p>
<h2>500x ошибка.</h2>
<p>Если после запроса к нашему серверу транзакция вернулась с 500х ошибкой, это означает, что она в обработку не поступила из за технических проблем на стороне нашего сервера или клиента. На обработку в банк такие транзакции не попадают. Мы заранее информируем клиентов о проведении плановых технических работ на нашей стороне и на стороне банка, но от внеплановых технических сбоев никто не застрахован. Проверьте свою сетевую инфраструктуру на корректную работоспособность.</p>
<p><strong>Что делать</strong>: Если проблем не выявлено, напишите письмо на support@vepay.online с описанием проблемы и уточнения статуса нашего сервера.</p>
<h2>Задвоение платежей.</h2>
<p>Задвоение встречается, если есть несколько транзакций с одинаковыми vepayid или extid. В таком случае успешно проведена будет только 1 транзакция, а остальные при попадании в пулл банка на обработку должны быть отменены нашей системой. На обработку в банк такие транзакции не попадают.</p>
<p><strong>Что делать</strong>: напишите письмо на support@vepay.online с описанием проблемы для уточнения статуса по платежам.</p>
<h2>При успешной транзакции возвращается колбэк с ошибкой или не возвращается вообще.</h2>
<p>Операция в ЛК проведена успешно, но колбэк отсутствует или пришел с ошибкой. Это не значит, что транзакция прошла не успешно. В большинстве случаев операция прошла успешно, но из-за технических проблем, банк отправил некорректный ответ на наш сервер или наш сервер некорректно принял ответ. В этом случае колбэк может быть отменен, либо в теле колбэка доставлен ответ банка об ошибке.</p>
<p><strong>Что делать</strong>: напишите письмо на support@vepay.online с описанием проблемы для уточнения статуса по колбэку.</p>





           </AdaptiveDevBlock>
        </div>
    )
}

export default FAQ