import React from 'react'

import styles from './Housing.module.scss'
import topImg from '../../../img/housing/housingTopImg1.png'
import topImg2 from '../../../img/housing/housingTopImg2.png'
import housingIcon1 from '../../../img/housing/housingIcon1.svg'
import housingIcon2 from '../../../img/housing/housingIcon2.svg'
import housingIcon3 from '../../../img/housing/housingIcon3.svg'
import housingIcon4 from '../../../img/housing/housingIcon4.svg'
import housingIcon5 from '../../../img/housing/housingIcon5.svg'
import TopBlockWithImageNEW from '../../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import SolutionsCard from '../../../components/Cards/SolutionsCard/SolutionsCard'
import MaxWidthBlock from '../../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

const Housing = (props) => {

  const housingPayments = [
    {
      image: housingIcon1,
      header: 'Расщепление платежей ',
      text: 'Расщепление платежей и перечисление денежных средств ресурсным поставщикам жилищно-коммунальных услуг.'
    },
    {
      image: housingIcon2,
      header: 'Реестр платежей ',
      text: 'Отправка реестра платежей реализуется в удобном формате в режиме реального времени. Получение дополнительной информации (показание счетчиков и другое).'
    },
    {
      image: housingIcon3,
      header: 'Выставление счетов ',
      text: 'Автоматическая отправка ссылок на оплату счетов жилищно-коммунальных услуг по электронной почте или с помощью SMS.'
    },
    {
      image: housingIcon5,
      header: 'Мобильное приложение ',
      text: 'Готовый SDK модуль для выстраивания оплаты в мобильных устройствах с возможностью приема платежей через QR-код.'
    },
    {
      image: housingIcon4,
      header: 'Получение платежей ',
      text: 'Прием платежей на сайте или в мобильном приложении по технологии AFT. Организация сбора денежных средств на расчетный счет компании в режиме реального времени. Ввиду более низкого уровня одобрения AFT операций, VEPAY разработал многоуровневую систему маршрутизации, значительно снизив затраты партнера.'
    },
  ]

    return (
        <div className={styles.Housing}>
            <TopBlockWithImageNEW
              topBlockImg={topImg}
              topBlockImgNarrow={topImg2}
              topBlockItemText1='Набор платежных инструментов для компаний в сфере ЖКХ'
              topBlockItemText2='Инновационные инструменты для организации оплаты и автоматизации данных.'
            />

            <ColouredBGBlock>
              <MaxWidthBlock>
                <div className={styles.solutionsBlock}>
                  {
                    housingPayments.map((item, i) => <SolutionsCard {...item} key={i} />)
                  }
                </div>
              </MaxWidthBlock>
            </ColouredBGBlock>
        </div>
    )
}

export default Housing