import React from 'react'

import styles from './ShopscriptModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/shopscript-module-img.jpg'
import img2 from '../../../img/developers/CMS-modules/shopscript-module-img2.jpg'
import img3 from '../../../img/developers/CMS-modules/shopscript-module-img3.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const ShopscriptModule = (props) => {

    return (
        <div className={styles.ShopscriptModule}>
           <AdaptiveDevBlock>
            <h1>Shop-Script модуль</h1>
            <h3>
            Инструкция по установке
            </h3>
            <p>
            1. Скачайте архив vepay-1.0.0.zip.
            </p>
            <p>
            2. Распакуйте содержимое архива в корень вашего сайта.
            </p>
            <p>
            3. Откройте панель управления сайтом.
            </p>
            <p>
            4. Перейдите в Магазин  {'>'} Настройки  {'>'} Оплата.
            </p>
            <AdaptiveImg image={img1} />
            <p>
            5. Справа найдите “Добавить способ оплаты” и выберите Vepay.
            </p>
            <AdaptiveImg image={img2} />
            <p>
            6. На странице введите Merch Id, Merch Token и Callback Token взять их можно из личного кабинета и нажмите сохранить.
            </p>
            <AdaptiveImg image={img3} />




           </AdaptiveDevBlock>
        </div>
    )
}

export default ShopscriptModule