import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './PaymentWidget.module.scss'
import img1 from '../../../img/developers/payment-widget/payment-widget-img1.png'
import widget1 from '../../../img/developers/payment-form/widget1.png'
import widget2 from '../../../img/developers/payment-form/widget2.png'
import widget3 from '../../../img/developers/payment-form/widget3.png'
import widget4 from '../../../img/developers/payment-form/widget4.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const PaymentWidget = (props) => {

    return (
        <div className={styles.PaymentWidget}>
            <AdaptiveDevBlock>
            <h1>Платежный виджет</h1>
			
                <AdaptiveImg image={img1} />
				
				<p>
                <em>Общая схема взаимодействия</em>
				</p>
				<p>
					 1. Пользователь заходит на сайт продавца и выбирает услугу.
				</p>
				<p>
					 2. Пользователь нажимает на кнопку "оплатить" и инициирует вызов платежного виджета. В клиентской части веб-сервиса формируется набор параметров вызова виджета и передаётся к серверной части.
				</p>
				<p>
					 3. На стороне платёжной платформы выполняются подготовка виджета с учётом параметров вызова и передача к пользовательскому устройству данных для отображения.
				</p>
				<p>
					 4. Виджет отображается в пользовательском интерфейсе. Пользователь вводит реквизиты карты для оплаты, нажимает оплатить.
				</p>
				<p>
					 5. От виджета к платёжной платформе отправляется запрос на выполнение целевого действия с учётом всех данных, введённых пользователем.
				</p>
				<p>
					 6. На стороне платёжной платформы выполняется регистрация номера карты (если требуется) и регистрация платежа. Данные передаются в платёжную среду.
				</p>
				<p>
					 7. В платёжной среде открывается страница банка-эмитента. Пользователь подтверждает платеж с помощью кода из sms. Нажимает "оплатить". Выполняются проверка карты и обработка платежа. После успешной проверки обоих для карты формируется идентификатор. В платёжную платформу поступает информация о результате.
				</p>
				<p>
					 8. В платёжной платформе обрабатывается итоговая информация, после чего на заданный URL контрагента (если указан колбэк) отправляется программное оповещение о платеже.
				</p>
				<p>
					 9. Происходит перенаправление пользователя на страницу клиента в соответствии с результатом оплаты.
				</p>
				<p>
					 Эта схема отображает ключевые моменты со стороны партнера, но в отдельных случаях может варьироваться в зависимости от вида услуг.
				</p>
				<p>
 <br/>
 <em>Базовый пользовательский сценарий можно представить следующим образом:</em>
				</p>
				<p>
					 1.Пользователь подтверждает готовность оплатить услугу, переходит на страницу платежной формы и выбирает метод оплаты.
				</p>
                <AdaptiveImg image={widget1} />
				<p>
					 2. В открывшемся виджете для оплаты, пользователь указывает реквизиты карты, нажимает кнопку "оплатить".
				</p>
                <AdaptiveImg image={widget2} />
				<p>

				</p>
				<p>
					 3. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты. Вводит код подтверждения из sms, нажимает кнопку "отправить".
				</p>
                <AdaptiveImg image={widget3} />
				<p>
					 4. Пользователь получает информацию о результате оплаты.
				</p>
                <AdaptiveImg image={widget4} />
			













            </AdaptiveDevBlock>
        </div>
    )
}

export default PaymentWidget