import React from 'react'

import styles from './SelfEmployed.module.scss'
import selfEmployedTopImg from '../../img/self-employed/selfEmployedTopImg.png'
import selfEmployedTopImgNarrow from '../../img/self-employed/selfEmployedTopImg2.png'
import paymentSteps from '../../img/self-employed/selfEmployedPaymentsImg1.png'
import startListImg from '../../img/self-employed/startListImg.png'
import AdaptiveParagraphs from '../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'
import SelfEmployedTariffs from './SelfEmployedTariffs/SelfEmployedTariffs'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'


const SelfEmployed = (props) => {

    const topBlockItemText1 = 'Выплаты самозанятым и физическим лицам'
    const topBlockItemText2 = 'Переводите деньги исполнителям на российские и зарубежные банковские карты, в электронные кошельки и на российские банковские счета. Быстрые и удобные выплаты физлицам по API или по списку.'

    const youLikeList = [
        {
            title: 'Простой процесс, мгновенные транзакции',
            text: 'Данные о получателях и суммах можно передать нам по API. Выплата отправляется мгновенно, а исполнитель получает её в течение нескольких минут. Отчёты о всех выплатах мы формируем ежедневно и отправляем вам на почту.'
        },
        {
            title: 'Интеграция по API',
            text: 'Мы сфокусировались на продукте, и автоматизируем то, что вы привыкли делать руками. Сделали API понятным и добавили возможность тестирования до работы с реальными данными.'
        },
        {
            title: 'Соответствие требованиям',
            text: 'Мы проверяем правомерность основания для выплат. Все выплаты проводятся в строгом соответствии с Налоговым кодексом и 115-ФЗ.'
        },
        {
            title: 'Прямая интеграция с ФНС',
            text: 'VEPAY аккредитован ФНС. Мы проверяем налоговый статус получателя, регистрируем самозанятых, формируем фискальный чек за выплаты, перечисляем налог.'
        },
        {
            title: 'Ответственный подход к вопросам безопасности',
            text: 'Персональные и платежные данные получателей получаем и храним, соблюдая все требования законов и платежных систем. Все запросы к API передаются в зашифрованном виде.'
        },
    ]

    const startList = [
        {
            text: 'Отправьте нам заявку'
        },
        {
            text: 'Мы подберём решение для вашего бизнеса'
        },
        {
            text: 'Проведём идентификацию, проверим документы'
        },
        {
            text: 'Подпишем договор'
        },
        {
            text: 'Готово, можно начинать интеграцию нашему API'
        },
    ]

    return (
        <div className={styles.SelfEmployed}>

            <TopBlockWithImageNEW
                topBlockImg={selfEmployedTopImg}
                topBlockImgNarrow={selfEmployedTopImgNarrow}
                topBlockItemText1={topBlockItemText1}
                topBlockItemText2={topBlockItemText2}
                centeredBlockStyle={styles.centeredBlockStyle}
            />
            <ColouredBGBlock>
                <MaxWidthBlock>
                    <h1 className={styles.centered} >Почему VEPAY</h1>
                    <div>
                        <AdaptiveParagraphs>
                            <p>Подходит российским и международным сервисам, работающим с физлицами: фрилансерами, дистанционными специалистами и распределёнными командами.</p>
                            <p><strong>Сервисам доставки и интернет-магазинам</strong>, чтобы платить курьерам</p>
                            <p><strong>Образовательным онлайн-платформам</strong>, чтобы платить репетиторам и преподавателям</p>
                            <p>Полезно тем, кто работает или только планирует работать с самозанятыми (в России или за рубежом).</p>
                            <p> <strong>Биржам фрилансеров</strong>, чтобы платить дизайнерам, копирайтерам, фотографам, веб-мастерам, переводчикам, разработчикам — и другим исполнителям</p>

                        </AdaptiveParagraphs>

                    </div>
                </MaxWidthBlock>

                <div className={styles.paymentStepsBlock}>
                    <div className={styles.stepImgBlock}>
                        <img className={styles.paymentStepsImg} src={paymentSteps} alt="paymentSteps" />
                        <h1>КАК ПРОХОДЯТ ВЫПЛАТЫ САМОЗАНЯТЫМ</h1>
                    </div>
                    <div className={styles.paymentStepsWrap}>
                        <div className={styles.paymentSteps}>

                            <div className={styles.step}>
                                <p className={styles.stepNumber}>1</p>
                                <p >
                                    <span>Проверка</span>
                                Проверим налоговый статус физлица по ИНН — напрямую в ФНС.
                                </p>
                            </div>
                            <div className={styles.step}>
                                <p className={styles.stepNumber}>
                                    2
                                </p>
                                <p >
                                    <span>Регистрация</span>
                                    Зарегистрируем получателя выплаты как самозанятого. Разумеется, с его согласия.
                                </p>
                            </div>
                            <div className={styles.step}>
                                <p className={styles.stepNumber}>
                                    3
                                </p>
                                <p >
                                    <span>Выплаты</span>
                                    Перечислим деньги на банковский счёт, карту или в электронный кошелек. Вычислим и заплатим налог за человека (в следующем месяце, как полагается).
                                </p>
                            </div>
                            <div className={styles.step}>
                                <p className={styles.stepNumber}>
                                    4
                                </p>
                                <p >
                                    <span>Подтверждение</span>
                                    Пришлём вам чек для бухгалтерии. Это обязательный документ для учета расходов компании при определении налоговой базы.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                <MaxWidthBlock>
                    <h1 className={styles.youLikeIt}>ВАМ ПОНРАВИТСЯ</h1>
                    <div>
                        <AdaptiveParagraphs>
                            {
                                youLikeList.map(item => <ParagraphWithTitle {...item} key={item.title} />)
                            }
                        </AdaptiveParagraphs>
                    </div>

                    
                </MaxWidthBlock>

                <div className={styles.startListBlock}>
                    
                    <img className={styles.startListImg} src={startListImg} alt="startList"/>
                    <div className={styles.startListBlockItem}>

                        <div className={styles.startContent}>
                            <h1>С чего начать</h1>
                            <div className={styles.startList}>
                                {
                                    startList.map(item => <TextWithTick className={styles.tickedText} {...item} type={'green'} key={item.text}/>)
                                }
                            </div>
                            <SelfEmployedTariffs/>
                        </div>
                        
                    </div>


                </div>


            </ColouredBGBlock>


            



        </div>
    )
}

export default SelfEmployed