import React from 'react'

import styles from './TextWithTick.module.scss'

const TextWithTick = ({ text, type = 'violet', className, children }) => {

    const cls = [
        styles.TextWithTick,
        styles[type],
        className
    ]


    return (
        <h3 className={cls.join(' ')}>
             {text}
             {children}
        </h3>
    )
}

export default TextWithTick