import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './InvoiceForming.module.scss'
import img1 from '../../../img/developers/invoice-forming/forming-invoice-img1.png'
import img2 from '../../../img/developers/invoice-forming/forming-invoice-img2.png'
import img3 from '../../../img/developers/invoice-forming/forming-invoice-img3.png'
import img4 from '../../../img/developers/invoice-forming/forming-invoice-img4.png'
import img5 from '../../../img/developers/invoice-forming/forming-invoice-img5.png'
import img6 from '../../../img/developers/invoice-forming/forming-invoice-img6.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const InvoiceForming = (props) => {

    return (
        <div className={styles.InvoiceForming}>
           <AdaptiveDevBlock>
           <h1>Формирование счета (Мерчант)</h1>
				<p>
					 1. Раздел "Виджет":
				</p>
				<AdaptiveImg image={img1} />
				<p>
					 2. Создать счет:
				</p>
				<AdaptiveImg image={img2} />
				<p>
					 3. После создания счет отображается в списке, также отображается статус его оплаты:
				</p>
				<AdaptiveImg image={img3} />
				<p>
					 При нажатии «Отменить» — счет будет недоступен к оплате. «Повторно» — отправить повторно ссылку для оплаты на email.
				</p>
				<p>
 <br/>
					 4. После создания счета, клиенту приходит уведомление на email со ссылкой на форму оплаты.
				</p>
				<p>
					 Пользовательский сценарий:
				</p>
				<p>
					 1. На форме для оплаты указаны: название сервиса, где клиент совершал заказ, сумма, которую он собирается оплатить и комиссия
				</p>
				<p>
					 2. Клиент указывает данные карты и оплачивает заказ
				</p>
				<p>
					 После отправки суммы на стороне платёжной платформы выполняется регистрация платежа.
				</p>
				<p>
					 3. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты, вводит код подтверждения из sms (рис.3)
				</p>
				<p>
					 После проверки карты с нее списывается сумма на счет Vepay.
				</p>
				<p>
					 4. Пользователю открывается виджет со статусом оплаты на странице Vepay или на странице веб-сервиса, к которой выполняется перенаправление (
				</p>
				<AdaptiveImg image={img4} />
				<p>
					 5. После успешной оплаты, на email клиента приходит электронный чек с данными по оплате.
				</p>
				<AdaptiveImg image={img5} />
				<p>
					 6. После успешной оплаты в личном кабинете мерчанта статус счета сменяется на оплачен.
				</p>
                <AdaptiveImg image={img6} />
				<p>
					 По-умолчанию платежная ссылка действительная 20 минут, после чего при переходе на нее отображается 404-ошибка.
				</p>













           </AdaptiveDevBlock>
        </div>
    )
}

export default InvoiceForming