import React from 'react'
import { Link } from 'react-router-dom'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './MerchantRecurring.module.scss'

const MerchantRecurring = (props) => {

    return (
        <div className={styles.MerchantRecurring}>
           <AdaptiveDevBlock>
           <h1>Рекарринг</h1>
            <p>
            Для сервиса Мерчант существует система периодических платежей. Данный метод используется при оформлении товара в рассрочку. Клиент интернет-магазина осуществляет первый взнос, используя вызов платежной формы с вводом всех необходимых данных по карте. После первого успешного взноса за данным клиентом сохраняется токен карты, позволяющий автоматически списывать средства с его баланса без ввода реквизитов и вызова платежной формы.
            </p>
            <p>
            Описание API Recarring находится в разделе: <Link to={'/developers/recurring-payments'} >Рекарринг и периодические платежи</Link>.
            </p>
           </AdaptiveDevBlock>
        </div>
    )
}

export default MerchantRecurring