import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './SBP.module.scss'

const SBP = (props) => {

    return (
        <div className={styles.SBP}>
           <AdaptiveDevBlock>
           <h1>Система быстрых платежей (СБП)</h1>
				<p>
1. Выплата на карту<br/>
POST <a href="https://api.vepay.online/mfo/out/paycard-sbp">https://api.vepay.online/mfo/out/paycard-sbp</a>
					<pre>
<code>
{`{

  "cardnum": 4314090010072480,
  "amount": 100,
  "extid": "5112a",
  "document_id": "221",
  "firstname": "",
  "secondname": "",
  "patronymic": "",
  "phone": "0079009000102"
}`}
</code></pre>

Ответ:

<pre><code>
{`{
  "status": 1,
  "message": "string",
  "id":101130
}`}
</code></pre>
</p>

<p>
2. Погашение<br/>
POST <a href="https://api.vepay.online/mfo/pay/lk-sbp">https://api.vepay.online/mfo/pay/lk-sbp</a>
<pre>
<code>
{`{
  "type": 3,
  "amount": 13,
  "extid": "32124411",
  "descript": "descript..",
  "successurl": "",
  "canselurl": "",
  "failurl": "",
  "postback": "",
  "timeout": 50,
  "fullname": "Test TEst Test",
  "document_id": "Test document",
  "qrHeight": "<qrHeight>", - Высота QR-кода
  "qrWidth": "<qrWidth>" - Ширина QR-кода
}`}
</code>
</pre>

Ответ:

<pre><code>
{`{
  "status": 1,
  "message": "string",
  "id": 101133,
  "renderedQr": "" - QR-код в формате PNG, закодированный в Base64
}`}
</code></pre>
</p>









           </AdaptiveDevBlock>
        </div>
    )
}

export default SBP