import React from 'react'
import { NavLink } from 'react-router-dom'

import styles from './VepayLogo.module.scss'

import vapayWhite from '../../img/VEPAY-logo-white-text.svg'
import vapayBlack from '../../img/Vepay-black.svg'


const VepayLogo = ({className, type='header'}) => {

    const cls = [
        styles.logo,
        className
    ]
    
    return (
        <div className={styles.VepayLogo}>
                <NavLink to="/">
                    <img className={cls.join(' ')} src={type === 'sidemenu'? vapayBlack: vapayWhite} alt="VEPAY-logo-white-text.svg" />
                </NavLink>
        </div>
    )
}

export default VepayLogo