import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './LoanRepaymentScheme.module.scss'
import img1 from '../../../img/developers/loan-repayment/loan-repayment-img1.png'
import img2 from '../../../img/developers/loan-repayment/loan-repayment-img2.png'
import img3 from '../../../img/developers/loan-repayment/loan-repayment-img3.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const LoanRepaymentScheme = (props) => {

    return (
        <div className={styles.LoanRepaymentScheme}>
            <AdaptiveDevBlock>
            <h1>Схема "Погашение займа"</h1>
				<AdaptiveImg image={img1} />
				<p>
					 Погашение займа с карты из кабинета МФО.
				</p>
				<p>
					 1. Пользователь выбирает погасить займ на сайте МФО.
				</p>
				<p>
					 2. Пользователь нажимает кнопку погасить. В пользовательском интерфейсе открывается виджет с реквизитами для оплаты.
				</p>
				<p>
					 Пользователь вводит данные карты. Нажимает оплатить.
				</p>
				<p>
					 3. На стороне платёжной платформы выполняется регистрация платежа.
				</p>
				<p>
					 4. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты, вводит код подтверждения из sms.
				</p>
				<p>
					 5. После проверки карты с нее списывается сумма на счет Vepay.
				</p>
				<p>
					 6 В платёжную платформу поступает информация о статусе платежа.
				</p>
				<p>
					 7. Пользователю открывается виджет со статусом оплаты на странице Vepay или на странице веб-сервиса, к которой выполняется перенаправление.
				</p>
				<p>
					 Продавцу приходит колбэк о статусе оплаты.
				</p>
				<p>
 <br/>
					 Пользовательский сценарий:
				</p>
				<p>
					 1. Пользователь нажимает оплатить займ и переходит на страницу платежной формы.
				</p>
				<p>
					 2. В пользовательском интерфейсе открывается виджет с реквизитами для оплаты.
				</p>
				<p>
					 На форме для оплаты указаны: название сервиса для погашения займа, сумма, которую он собирается внести и комиссия.
				</p>
				<p>
					 Клиент указывает данные карты и выбирает оплатить.
				</p>
				<AdaptiveImg image={img2} />
				<p>
					 3. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты. Вводит код подтверждения из sms, нажимает кнопку "отправить".
				</p>
				<p>
					 4. Пользователь получает информацию о результате оплаты.
				</p>
				<AdaptiveImg image={img3} />
				<p>
					 &nbsp;
				</p>
				<h2>Пример API запроса на погашение займа</h2>
				<p>
					 Отправляется запрос на погашение займа:&nbsp;<a href="https://api.vepay.online/mfo/pay/lk">https://api.vepay.online/mfo/pay/lk</a>:
				</p>
				 <pre><code>
{`{

    "amount": 100,
    "document_id": "1a9a18a6-4355-4cb4",
    "fullname": "Ivanov Ivan Ivanovich",
    "extid": "101464",
    "timeout": 10,
    "successurl": ""https://exemple.com/successurl"",
    "failurl": ""https://exemple.com/failurl"",
    "cancelurl": "https://exemple.com/cancelurl"
}`}
</code>
</pre>
				<p>
					 Ответ:
				</p>
				 <pre><code>
{`{
    "status": 1,
    "message": "string",
    "id": 101133,
    "url": "https://test.vepay.online/pay/form/101133"
}`}
</code>
</pre>
				<p>
					 Пользователь переходит на страницу оплаты займа (параметр url в ответе), где вводит данные карты, с которой собирается погасить займ.
				</p>
				<p>
					 Подробный сценарий погашения займа можно посмотреть в разделе: Схема "Погашение займа"
				</p>
				<p>
					 Параметры запроса:
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 amount
					</td>
					<td>
						 Cумма платежа, руб
					</td>
				</tr>
				<tr>
					<td>
						 document_id
					</td>
					<td>
						 Номер договора
					</td>
				</tr>
				<tr>
					<td>
						 fullname
					</td>
					<td>
						 ФИО клиента
					</td>
				</tr>
				<tr>
					<td>
						 extid
					</td>
					<td>
						 Внешний идентификатор запроса, extid регистрации карты
					</td>
				</tr>
				<tr>
					<td>
						 timeout
					</td>
					<td>
						 Тайм-аут ожидания оплаты, от 10 до 59 минут
					</td>
				</tr>
				<tr>
					<td>
						 successurl
					</td>
					<td>
						 Адрес для Url для возврата после завершения платежа (успех)
					</td>
				</tr>
				<tr>
					<td>
						 failurl
					</td>
					<td>
						 Адрес для Url для возврата после завершения платежа (ошибка)
					</td>
				</tr>
				<tr>
					<td>
						 cancelurl
					</td>
					<td>
						 Адрес для Url для возврата после отказа от оплаты
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа:
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Статус (0 – ошибка 1 - принят)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Сообщение ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 Идентификатор запроса (при успехе)
					</td>
				</tr>
				<tr>
					<td>
						 url
					</td>
					<td>
						 Cсылка для перехода на форму оплаты (при успехе)
					</td>
				</tr>
				</tbody>
				</table>












            </AdaptiveDevBlock>
        </div>
    )
}

export default LoanRepaymentScheme