import React from 'react'

import styles from './ColouredBGBlock.module.scss'

const ColouredBGBlock = ({children}) => {

    return (
        <div className={styles.ColouredBGBlock}>
            <div className={styles.greenRound}></div>
            <div className={styles.purpleRound}></div>
            <div className={styles.violetRound}></div>
            <div className={styles.greenRound2}></div>
            <div className={styles.purpleRound2}></div>
            <div className={styles.violetRound2}></div>

           
            {children}
        </div>
    )
}

export default ColouredBGBlock