import React from 'react'
import Button from '../../UI/Button/Button'
import { connect } from 'react-redux'
import { openModal } from '../../../redux/actions/connectModalActions'

import styles from './TopBlockWithImageNEW.module.scss'
import { useHistory } from 'react-router-dom'



const TopBlockWithImageNEW = ({ topBlockImg, topBlockImgNarrow, topBlockItemText1, topBlockItemText2, buttonText='подключить', extraText, openModal, clickHandler, children, centeredBlockStyle }) => {

    const buttonClickHandler = () => {
        if (clickHandler) {
            clickHandler()
        } else {
            openModal()
        }
    }

    const history = useHistory()

    const centeredBlockCls = [
        styles.topBlockCentered,
        centeredBlockStyle
    ]

    return (
        <div className={styles.TopBlockWithImageNEW}>
            <img className={styles.topBlockImg} src={topBlockImg} alt="topBlockImg"/>
            <img className={styles.topBlockImgNarrow} src={topBlockImgNarrow} alt="topBlockImgNarrow"/>
            <div className={styles.topBlockItem}>
                <div className={centeredBlockCls.join(' ')}>
                    <div  className={styles.textBlock}>
                        <h1 className={styles.topBlockItemText1}>{topBlockItemText1}</h1>
                        <p className={styles.topBlockItemText2}>{topBlockItemText2}</p>
                    </div>
                    <div className={styles.topButton}>
                        <Button 
                            text={buttonText} 
                            type={'green'}
                            clickHander={buttonClickHandler}
                            

                        />
                    </div>
                </div>
            </div>
            {extraText && (
                <div className={styles.buttonsBlock}>
                <div 
                    className={styles.selfEmployed}
                    onClick={() => history.push('/self-employed')}
                >
                    <span>Самозанятым</span>
                </div>
                <div 
                    className={styles.legalEntity}
                    onClick={() => history.push('/legal-entity')}
                >
                    <span>Юр лицам</span>
                </div>

            </div>
            )}

            {children}
        </div>
    )
}

export default connect(null, {openModal})(TopBlockWithImageNEW)