import React from 'react'

import styles from './OpenCartModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/opencart-module-img.png'
import img2 from '../../../img/developers/CMS-modules/opencart-module-img2.png'
import img3 from '../../../img/developers/CMS-modules/opencart-module-img3.png'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const OpenCartModule = (props) => {

    return (
        <div className={styles.OpenCartModule}>
            <AdaptiveDevBlock>
                <h1>OpenCart модуль</h1>
                <h3>Инструкция по установке</h3>
                <p>1. Скачайте архив opencart-vepay.ocmod.zip</p>
                <p>
                2. Загрузите архив на сайт.
                </p>
                <AdaptiveImg image={img1} />
                <p>
                3. Настройте модуль в расширениях
                </p>
                <AdaptiveImg image={img2} />
                <p>
                4. Убедитесь, что в настройках валют есть RUB.
                </p>
                <AdaptiveImg image={img3} />









            </AdaptiveDevBlock>
        </div>
    )
}

export default OpenCartModule