import React from 'react'

import styles from './HomePage.module.scss'
import topBlockImg from '../../img/homepage/homepage-top-img2.png'
import topBlockImgNarrow from '../../img/homepage/homepage-top-img3.png'
import vepayLogoWhite from '../../img/VEPAY-logo-white.svg'
import vepayMobileApp from '../../img/legal-entity-main/vepay-mobile-app.png'
import vepayCreativePic from '../../img/homepage/vepay-creative-picture.png'
import applePay from '../../img/homepage/applePay.svg'
import unionPay from '../../img/homepage/unionPay.svg'
import masterCard from '../../img/homepage/masterCard.svg'
import samsungPay from '../../img/homepage/samsungPay.svg'
import solutionsImg from '../../img/homepage/solutionsImg.png'
import prefooterImg from '../../img/homepage/prefooterImg.png'
import purpleBG from '../../img/homepage/purpleBG.png'
import mtsBank from '../../img/homepage/mtsBank.svg'
import bank131 from '../../img/homepage/bank131.svg'
import tkb from '../../img/homepage/tkb.svg'
import decorations from '../../img/homepage/decorations.svg'

import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import Button from '../../components/UI/Button/Button'
import MySlider from '../../components/UI/MySlider/MySlider'

const HomePage = ({history, ...props}) => {


    return (
        <div className={styles.HomePage}>
            <TopBlockWithImageNEW
                topBlockImg={topBlockImg}
                topBlockImgNarrow={topBlockImgNarrow}
                topBlockItemText1={'Отправляйте и получайте деньги с помощью VEPAY'}
                topBlockItemText2={'Прием платежей. Выставление счетов. Осуществление выплат. Настройка автоплатежей через QR-код. Онлайн кредитование. Онлайн-касса. И всё это VEPAY!'}
                extraText={true}
            />
            <ColouredBGBlock>
                <MaxWidthBlock>
                    <div className={styles.grayBGIntro}>
                        <div className={styles.centeredBlock}>
                            <div className={styles.introBlock}>
                                <div>
                                    <img src={vepayLogoWhite} alt="vepayLogoWhite"/>
                                </div>
                                <div>
                                    <h2>как IT компания</h2>
                                    <p>
                                    платежный провайдер и прямой эквайер, лицензированный FCA — Управлением по финансовому регулированию и контролю
                                    </p>
                                </div>
                            </div>
                            <div className={[styles.introBlock, styles.hidePic].join(' ')}>
                                <img className={styles.creativeImg} src={vepayCreativePic} alt="vepayCreativePic"/>

                            </div>
                            <div className={styles.introBlock}>
                                <div>
                                    <img src={vepayLogoWhite} alt="vepayLogoWhite"/>
                                </div>
                                <div>
                                    <h2>как процессинговая компания</h2>
                                    <p>
                                    платежный провайдер и прямой эквайер, лицензированный FCA — Управлением по финансовому регулированию и контролю
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={styles.advantages}>
                        <div className={styles.advantagesBlock}>
                            <img className={styles.mobileAppImg}  src={vepayMobileApp} alt="vepayMobileApp"/>
                            <img className={styles.decorations}  src={decorations} alt="decorations"/>
                        </div>
                        <div className={styles.advantagesBlock}>
                            <div className={styles.advantagesText}>
                                <h1>Преимущества VEPAY</h1>
                                <div>
                                    <p>
                                    Работаем для вас с 2010 года. За время своего существования компания VEPAY прошла путь от приема платежей до полноценного процессингового центра, предоставляющего решения в области платежных технологий. 
                                    </p>
                                    <p>
                                    Основным направлением работы VEPAY являются отрасли МФО и финансовые организации, ЖКХ, интернет-магазины и маркетплейс, краудфандинг-платформы, сервисы онлайн-образования.
                                    </p>
                                </div>
                                <div>
                                    <Button
                                        text='Подробнее'
                                        clickHander={() => {
                                            history.push('/receiving-payments')
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>

                    <MySlider>
                        {
                            [
                            applePay,
                            unionPay,
                            masterCard,
                            samsungPay
                            ].map((image, i) => {
                                return (
                                <div className={styles.sliderImageWrap} key={i}>
                                    <div className={styles.sliderImageBlock}>
                                        <img className={i === 2? styles.masterCard : styles.sliderImage} src={image} alt="sliderImage"/>
                                    </div>
                                </div>
                                )
                            })
                        }
                    </MySlider>

                    {/* <div className={styles.payMethods}>
                        <img src={applePay} alt="applePay"/>
                        <img src={unionPay} alt="unionPay"/>
                        <img className={styles.masterCard} src={masterCard} alt="masterCard"/>
                        <img src={samsungPay} alt="samsungPay"/>

                    </div> */}

                    <div className={styles.solutions}>
                        <div className={styles.solutionsBlock}>
                            <div className={styles.solutionsText}>
                                <h1>Готовые решения для приема платежей</h1>
                                <div>
                                    <p>
                                    Оплата на сайте, в мобильном приложении или торговом зале. В любом месте: в интернет-магазине, чате или приложении. В любое время. Любым способом: банковской картой, с помощью мобильного телефона и даже в кредит.
                                    </p>
                                </div>
                                <div>
                                    <Button
                                        text='Подробнее'
                                        clickHander={() => {
                                            history.push('/industry-solutions')
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className={styles.solutionsBlock}>
                            <img className={styles.solutionsImg} src={solutionsImg} alt="solutionsImg"/>
                            <img className={styles.decorations}  src={decorations} alt="decorations"/>
                            <img className={styles.purpleBG}  src={purpleBG} alt="purpleBG"/>
                        </div>
                    </div>

                    <div className={styles.banks}>
                        <img src={mtsBank} alt="mtsBank"/>
                        <img src={bank131} alt="bank131"/>
                        <img src={tkb} alt="tkb"/>

                    </div>

                </MaxWidthBlock>
            </ColouredBGBlock>

            <div>
                <img className={styles.prefooterImg}  src={prefooterImg} alt="prefooterImg"/>
            </div>


        </div>
    )
}

export default HomePage