import React from 'react'

import styles from './InternetShops.module.scss'
import topImg from '../../../img/internet-shops/internetShopsTopImg1.png'
import topImg2 from '../../../img/internet-shops/internetShopsTopImg2.png'
import widgetImg from '../../../img/internet-shops/widget.png'
import iShopImg1 from '../../../img/internet-shops/iShopImg1.svg'
import iShopImg2 from '../../../img/internet-shops/iShopImg2.svg'
import iShopImg3 from '../../../img/internet-shops/iShopImg3.svg'
import iShopImg4 from '../../../img/internet-shops/iShopImg4.svg'
import iShopImg5 from '../../../img/internet-shops/iShopImg5.svg'
import iShopImg6 from '../../../img/internet-shops/iShopImg6.svg'
import iShopImg7 from '../../../img/internet-shops/iShopImg7.svg'
import iShopImg8 from '../../../img/internet-shops/iShopImg8.svg'
import AdaptiveColumn from '../../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import TopBlockWithImageNEW from '../../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import TextWithCircle from '../../../components/UI/TextWithCircle/TextWithCircle'
import SolutionsCard from '../../../components/Cards/SolutionsCard/SolutionsCard'

const InternetShops = (props) => {

  const widgetAdvantagesList = [
    'Не требует изменений на сайте и перехода на платежный шлюз; ',
    'Различные варианты проведения платежа; ',
    'Возможность изменений формы виджета от самой минимальной до полноценного маркетплейса с формированием корзины покупателя из различных услуг; ',
    'Автоматическое определение банка-эмитента и платежной системы (Visa, MasterCard или Мир); ',
    'Указание почтового адреса или номера телефона для получения электронного чека; ',
    'Возможность оформления подписки или реккурентного платежа для приобретения товаров;',
    'Кастомизация платежной формы виджета в цветах сайта интернет-магазина; ',
    'Безопасность проведения платежей гарантирована стандартами PCI DSS и Anti-froud системой.',
  ]

  const paymentMethodsList = [
    {
      header: 'Банковские карты ',
      image: iShopImg1,
      text: 'Mastercard, Visa, МИР'
    },
    {
      header: 'Мобильная коммерция ',
      image: iShopImg2,
      text: 'МТС, Билайн, Мегафон, Теле2'
    },
    {
      header: 'Сервисы бесконтактной оплаты ',
      image: iShopImg3,
      text: 'Apple Pay, Google Pay, Samsung Pay'
    },
    {
      header: 'Оплата в кредит ',
      image: iShopImg4,
      text: 'Быстрый и современный способ покупки товаров в интернет- магазине.'
    },
    {
      header: 'QR-код ',
      image: iShopImg5,
      text: 'Оплата заказа по сформированному QR-коду.'
    },
    {
      header: 'Электронные кошельки ',
      image: iShopImg6,
      text: 'в разработке'
    },
    {
      header: 'Оплата по счету ',
      image: iShopImg7,
      text: 'Автоматическое формирование счета по заданным реквизитам.'
    },
    {
      header: 'WeChat ',
      image: iShopImg8,
      text: 'в разработке'
    },
  ]

    return (
        <div className={styles.InternetShops}>

            <TopBlockWithImageNEW
              topBlockImg={topImg}
              topBlockImgNarrow={topImg2}
              topBlockItemText1='Для интернет-магазинов'
              topBlockItemText2='Разнообразные и удобные способы оплаты в интернет-магазине для повышения конверсии продаж и увеличения лояльных клиентов. Оплата заказа осуществляется на сайте интернет-магазина или в мобильном приложении.'
            />

            <ColouredBGBlock>
               <MaxWidthBlock>

                <div className={styles.widgetBlock}>
                    <div className={styles.widgetHeader}>
                        <h1>Платежный виджет</h1>
                        <p>
                        Всплывающая платежная форма для мгновенной оплаты на сайте без перехода на дополнительный платежный шлюз. Это наиболее простой способ подключить оплату на сайте.
                        </p>
                    </div>
                  
                    <AdaptiveColumn>
                      <img  className={styles.widgetImg} src={widgetImg} alt="widget"/>
                    </AdaptiveColumn>
                    <AdaptiveColumn  className={styles.tickedBlock}>
                        {
                          widgetAdvantagesList.map(item => <TextWithCircle type={'green'} className={styles.tickedText} text={item} key={item}/>)
                        }
                    </AdaptiveColumn>
                  </div>

                  <div className={styles.paymentMethodsBlock}>
                      <h1 className={styles.paymentsTitle}>Способы оплаты для интернет-магазина</h1>
                      <div className={styles.payments}>
                        {
                          paymentMethodsList.map(item => <SolutionsCard className={styles.pText} {...item} key={item.title}/>)
                        }
                          
                      </div>
                  </div>

               </MaxWidthBlock>
            </ColouredBGBlock>

           

        </div>
    )
}

export default InternetShops
