import React from 'react'

import styles from './WordpressModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/wordpress-module-img.jpg'
import img2 from '../../../img/developers/CMS-modules/wordpress-module-img2.jpg'
import img3 from '../../../img/developers/CMS-modules/wordpress-module-img3.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const WordpressModule = (props) => {

    return (
        <div className={styles.WordpressModule}>
            <AdaptiveDevBlock>
                <h1>WordPress модуль</h1>
                <h3>
                Инструкция по установке
                </h3>
                <p>
                1. Скачайте архив с модулем
                </p>
                <p>
                2. Скопируйте файлы из архива в директорию / {'<'}корень сайта {'>'}/wp-content/plugins/
                </p>
                <p>
                3. Установите и активируйте плагин WooCommerce из маркетплейса
                </p>
                <p>
                4. В админ-панели сайта перейдите в раздел “Плагины” {'>'} “Установленные” и нажмите “Активировать” напротив плагина VEPAY
                </p>
                <AdaptiveImg image={img1} />
                <p>
                5. В той же админ-панели перейдите в раздел “WooCommerce” {'>'} “Настройки”, перейдите на вкладку “Платежи” и выберите управление VEPAY
                </p>
                <AdaptiveImg image={img2} />
                <p>
                6. Выполните необходимые настройки и нажмите “Сохранить изменения”
                </p>
                <AdaptiveImg image={img3} />













            </AdaptiveDevBlock>
        </div>
    )
}

export default WordpressModule