import React from 'react'
import { Link } from 'react-router-dom'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './AutoPayment.module.scss'

const AutoPayment = (props) => {

    return (
        <div className={styles.AutoPayment}>
            <AdaptiveDevBlock>
            <h1>Подключение автоплатежа</h1>
				<p>
					Для сервиса МФО существует метод автоплатежа. Данный метод используется для автоматического списания денежных средств по займу со счета клиента. Заемщик осуществляет первый тестовый платеж, используя вызов платежной формы с вводом всех необходимых данных по карте. После первого успешного платежа за данным клиентом сохраняется идентификатор карты, позволяющий автоматически списывать средства с его баланса без ввода реквизитов и вызова платежной формы.
				</p>
				<p>
					Это дает возможность:
				</p>
				<ul>
					<li>совершать оплату с помощью банковской карты «в один клик» без необходимости ввода платежных реквизитов.</li>
					<li>выполнять регулярные списания денежных средств без участия плательщика для инициации очередного платежа.</li>
					<li>запланировать режим рекуррентных списаний - когда происходит просрочка, клиенту приходит запрос на списание денежных средств со счета.</li>
				</ul>
				<p>
					Для подключения автоплатежа клиент должен совершить первый тестовый платеж, после чего данные его карты сохраняются в системе.
				</p>
				<p>
					Клиент при намерении оплатить займ на вашем сайте, соглашается с Вашими условиями по автоматическому списанию средств со счета его банковской карты.
				</p>
				<p>
					Схема подключения автоплатежа аналогична схеме в разделе "<Link to="/developers/card-registration">Регистрация карты</Link>":
				</p>
				<p>
					1. Клиент выбирает услугу, нажимает "оплатить".<br/>
					2. Клиент вводит реквизиты карты для первой тестовой оплаты<br/>
					3. На стороне платёжной платформы выполняется регистрация номера карты и регистрация платежа. Данные передаются в платёжную среду. <br/>
					4. В платёжной среде открывается страница банка-эмитента. Пользователь подтверждает платеж с помощью кода из sms. Нажимает "оплатить". Выполняются проверка карты и обработка платежа. После успешной проверки обоих, для карты формируется идентификатор. В платёжную платформу поступает информация о результате. <br/>
					5. В платёжной платформе обрабатывается итоговая информация, после чего на заданный URL контрагента отправляется оповещение об успешном результате.<br/>
					6. Информация о результате передаётся из платёжной платформы в Vepay.<br/>
					7. Информация о результате отображается в пользовательском интерфейсе в соответствии с заданными настройками: на странице Vepay или на странице веб-сервиса, к которой выполняется перенаправление.<br/>
					8. Последующие оплаты происходят без обязательного ввода данных карты.
				</p>
<p>Пример подключения:</p>
<p>1. Создание запроса на регистрацию карты.</p>
<pre>
<code>
/mfo/card/reg

{`
{
  "type": 0,
  "extid": "123e4567-e89b-12d3-a456-426655440000",
  "timeout": 10,
  "successurl": "https://example.org/success",
  "failurl": "https://example.org/fail",
  "cancelurl": "https://example.org/cancel"
}`}
</code>
</pre>
<p>Ответ:</p>
<pre>
<code>
{`{
  "status": 1,
  "url": "https://example.org/123",
  "id": 123
}`}
</code>
</pre>
<p>После пользователь вбивает инфу о карте и совершается тестовое списание</p>

<p>2. Проверка тестового списания с карты.</p>
<pre>
<code>
/mfo/pay/state

{`
{
  "id": 123
}`}
</code>
</pre>
<p>Ответ:</p>
<pre><code>
{`{
  "status": 1
}`}
</code></pre>
<p>если платеж прошел</p>

<p>3. Получение инфы о карте</p>
<pre><code>
/mfo/card/get

{`
{
  "id": 123,
  "type": 0
}`}
</code></pre>
<p>Ответ:</p>
<pre><code>
{`{
  "status": 1,
  "message": "",
  "card": {
    "id": 14523,
    "num": "431409******2282",
    "exp": "05/21",
    "holder": "Иван Иванов"
  }
}`}
</code></pre>

<p>Погашение займа с карты автоплатежом.</p>
<pre><code>
/mfo/pay/auto

{`
{
  "card": 14523,
  "amount": 100,
  "extid": "123e4567-e89b-12d3-a456-426655440000"
}`}
</code></pre>
<p>Ответ:</p>
<pre><code>
{`{
  "status": 1,
  "id": 12345
}`}
</code></pre>











            </AdaptiveDevBlock>
        </div>
    )
}

export default AutoPayment