import React from 'react'
import Button from '../../UI/Button/Button'

import styles from './GrayCardWithImg.module.scss'

const GrayCardWithImg = ({ image, header, text, clickHander, className, bodyClass }) => {

  const cls = [
    styles.GrayCardWithImg,
    className
  ]

  const bodyCls = [
    styles.cardBody, 
    bodyClass
  ]

    return (
        <div className={cls.join(' ')}>
            <img className={styles.img} src={image} alt="productImg" />
            
            <div className={bodyCls.join(' ')}>
              <h3 className={styles.header}>{header}</h3>
              <p className={styles.text}>{text}</p>
              <Button
                  text={'ПОДРОБНЕЕ'}
                  type='green'
                  className={styles.btn}
                  clickHander={clickHander}

              />
            </div>
        </div>
    )
}

export default GrayCardWithImg