import React from 'react'
import { connect } from 'react-redux'
import { openModal } from '../../../redux/actions/connectModalActions'

import styles from './Button.module.scss'

const Button = ({ 
    text='подключить', 
    clickHander, 
    openModal, 
    type='purpleBG', 
    className,
    disabled=false
 }) => {

    const cls = [
        styles.Button,
        styles[type],
        className
    ]

    return (
            <button
                onClick={clickHander || openModal}
                className={cls.join(' ')}
                disabled={disabled}
            >
                {text}
            </button>
    )
}



export default connect(null, {openModal})(Button)