import React from 'react'
import { Route, Switch, withRouter } from 'react-router-dom'
import PersonalAccount from '../../pages/Developers/PersonalAccount/PersonalAccount';
import Introduction from '../../pages/Developers/Introduction/Introduction';
import Capabilities from '../../pages/Developers/Capabilities/Capabilities';

import styles from './DevelopersMain.module.scss'
import PaymentWidget from '../../pages/Developers/PaymentWidget/PaymentWidget';
import CardRegistration from '../../pages/Developers/CardRegistration/CardRegistration';
import ClientConnection from '../../pages/Developers/ClientConnection/ClientConnection';
import Connection from '../../pages/Developers/Connection/Connection';
import CardPaymentMFO from '../../pages/Developers/CardPaymentMFO/CardPaymentMFO';
import AccountPaymentMFO from '../../pages/Developers/AccountPaymentMFO/AccountPaymentMFO';
import PaymentForm from '../../pages/Developers/PaymentForm/PaymentForm';
import RecurringPayments from '../../pages/Developers/RecurringPayments/RecurringPayments';
import InvoiceForming from '../../pages/Developers/InvoiceForming/InvoiceForming';
import SplittingPayments from '../../pages/Developers/SplittingPayments/SplittingPayments';
import Crowdfunding from '../../pages/Developers/Crowdfunding/Crowdfunding';
import AutoPayment from '../../pages/Developers/AutoPayment/AutoPayment';
import CardRegistrationMFO from '../../pages/Developers/CardRegistrationMFO/CardRegistrationMFO';
import LoanRepaymentScheme from '../../pages/Developers/LoanRepaymentScheme/LoanRepaymentScheme';
import MerchantRecurring from '../../pages/Developers/MerchantRecurring/MerchantRecurring';
import OrderPaymentScheme from '../../pages/Developers/OrderPaymentScheme/OrderPaymentScheme';
import Uprid from '../../pages/Developers/Uprid/Uprid';
import ApplePay from '../../pages/Developers/ApplePay/ApplePay';
import BitrixModule from '../../pages/Developers/BitrixModule/BitrixModule';
import OpenCartModule from '../../pages/Developers/OpenCartModule/OpenCartModule';
import ShopscriptModule from '../../pages/Developers/ShopscriptModule/ShopscriptModule';
import WordpressModule from '../../pages/Developers/WordpressModule/WordpressModule';
import CScartModule from '../../pages/Developers/CScartModule/CScartModule';
import PrestashopModule from '../../pages/Developers/PrestashopModule/PrestashopModule';
import MagentoModule from '../../pages/Developers/MagentoModule/MagentoModule';
import SBP from '../../pages/Developers/SBP/SBP';
import FAQ from '../../pages/Developers/FAQ/FAQ';

const DevelopersMain = (props) => {
  const path = props.match.path
  

    return (
        <div className={styles.DevelopersMain}>
            <Switch>
              <Route path={`${path}/introduction`} component={Introduction} />
              <Route path={`${path}/capabilities`} component={Capabilities} />
              <Route path={`${path}/payment-form`} component={PaymentForm} />
              <Route path={`${path}/personal-account`} component={PersonalAccount} />
              <Route path={`${path}/card-registration`} component={CardRegistration} />
              <Route path={`${path}/client-connection`} component={ClientConnection} />
              <Route path={`${path}/connection`} component={Connection} />
              <Route path={`${path}/card-payment-mfo`} component={CardPaymentMFO} />
              <Route path={`${path}/account-payment-mfo`} component={AccountPaymentMFO} />
              <Route path={`${path}/payment-widget`} component={PaymentWidget} />
              <Route path={`${path}/recurring-payments`} component={RecurringPayments} />
              <Route path={`${path}/invoice-forming`} component={InvoiceForming} />
              <Route path={`${path}/splitting-payments`} component={SplittingPayments} />
              <Route path={`${path}/crowdfunding`} component={Crowdfunding} />
              <Route path={`${path}/auto-payment`} component={AutoPayment} />
              <Route path={`${path}/card-registration-mfo`} component={CardRegistrationMFO} />
              <Route path={`${path}/loan-repayment-scheme`} component={LoanRepaymentScheme} />
              <Route path={`${path}/merchant-recurring`} component={MerchantRecurring} />
              <Route path={`${path}/order-payment-scheme`} component={OrderPaymentScheme} />
              <Route path={`${path}/uprid`} component={Uprid} />
              <Route path={`${path}/apple-pay`} component={ApplePay} />
              <Route path={`${path}/bitrix-module`} component={BitrixModule} />
              <Route path={`${path}/opencart-module`} component={OpenCartModule} />
              <Route path={`${path}/shopscript-module`} component={ShopscriptModule} />
              <Route path={`${path}/wordpress-module`} component={WordpressModule} />
              <Route path={`${path}/cscart-module`} component={CScartModule} />
              <Route path={`${path}/prestashop-module`} component={PrestashopModule} />
              <Route path={`${path}/magento-module`} component={MagentoModule} />
              <Route path={`${path}/sbp`} component={SBP} />
              <Route path={`${path}/faq`} component={FAQ} />
              <Route component={Introduction} />
            </Switch>
        </div>
    )
}

export default withRouter(DevelopersMain)