import { SET_IS_LOGGED_IN, SET_LOGIN_ERRORS, SET_LOGIN_LOADING } from "../actions/actionTypes";

const initialState = {
  isLoggedIn: false,
  errors: null,
  loading: false,
  user: 'guest'
}

export default function loginReducer(state = initialState, action) {
  switch (action.type) {
    case SET_IS_LOGGED_IN:
      return {
        ...state, 
        isLoggedIn: action.status
      }
    case SET_LOGIN_ERRORS:
      return {
        ...state,
        errors: action.errors
      }
    case SET_LOGIN_LOADING:
      return {
        ...state,
        loading: action.status
      }
    default:
      return state;
  }
}