import React from 'react'
import Button from '../Button/Button'

import styles from './AccButton.module.scss'

const AccButton = ({ className, ...props }) => {

    const goToAccount = () => {
        window.location.href = 'https://api.vepay.online/partner/index'
    }

    const cls = [
        styles.AccButton,
        className,
    ]

    return (
        <Button
            text={'Войти'}
            type={'purple'}
            clickHander={goToAccount}
            {...props}
            className={cls.join(' ')}
        />
    )
}

export default AccButton