import React, { Fragment } from "react";
import { useEffect } from "react";
import { connect } from "react-redux";
import { closeModal } from "../../redux/actions/connectModalActions";

import styles from "./ConnectModal.module.scss";
import Button from "../UI/Button/Button";
import { useState } from "react";

const ConnectModal = ({ closeModal, isOpen, error }) => {
  const [formControls, setFormControls] = useState({
    email: "",
    name: "",
    phone: "",
    message: "",
    pdAgree: false,
  });

  const changeHandler = (e) => {
    // console.log(e.target.name);
    setFormControls({
      ...formControls,
      [e.target.name]: e.target.value,
    });
  };

  const checkHandler = () => {
    setFormControls({
      ...formControls,
      pdAgree: !formControls.pdAgree,
    });
  };

  const onSubmit = (e) => {
    e.preventDefault();
    closeModal();
    setFormControls({
      email: "",
      name: "",
      phone: "",
      message: "",
      pdAgree: false,
    });
  };

  const isActive =
    formControls.pdAgree &&
    formControls.email &&
    formControls.name &&
    formControls.phone;

  useEffect(() => {
    const escapeClose = (e) => {
      if (e.key === "Escape") {
        closeModal();
      }
    };
    if (isOpen) {
      // document.body.style = "overflow: hidden; padding-right: 15px;";
      document.addEventListener("keyup", escapeClose);
    } else {
      // document.body.style = "";
      document.removeEventListener("keyup", escapeClose);
    }
  }, [isOpen, closeModal]);

  return (
    isOpen && (
      <Fragment>
        <div className={styles.backDrop} onClick={closeModal}></div>

        <div className={styles.ConnectModal}>
          <div className={styles.ConnectModalBody}>
            <span className={styles.modalCloseButton} onClick={closeModal}>
              &times;
            </span>
            <div className={styles.modalFormBlock}>
              <form
                id="connect-modal-form"
                className={styles.modalForm}
                onSubmit={onSubmit}
              >
                <div>
                  <p>Электронная почта</p>
                  <input
                    type="email"
                    name="email"
                    onChange={changeHandler}
                    value={formControls.email}
                    id="email"
                    required
                    placeholder="example@example.ru"
                  />
                </div>
                <div>
                  <p>Ваше имя</p>
                  <input
                    type="text"
                    name="name"
                    onChange={changeHandler}
                    value={formControls.name}
                    id="name"
                    required
                    placeholder="Иван Иванов"
                  />
                </div>
                <div>
                  <p>Номер телефона</p>
                  <input
                    type="text"
                    name="phone"
                    onChange={changeHandler}
                    value={formControls.phone}
                    id="phone"
                    required
                    placeholder="+7 922 555 555 55"
                  />
                </div>
                <div>
                  <p>Сообщение</p>
                  <textarea
                    name="message"
                    onChange={changeHandler}
                    value={formControls.message}
                    id="message"
                    placeholder="Текст вашего запроса"
                  ></textarea>
                </div>
                <div className={styles.modalFormItem}>
                  <input
                    type="checkbox"
                    onChange={checkHandler}
                    checked={formControls.pdAgree}
                    name="pdAgree"
                    id="pd-agree"
                  />
                  <label htmlFor="pd-agree">
                    {" "}
                    даю согласие на обработку персональных данных, в соответсвии
                    с политикой защиты и обработки персональных данных
                  </label>
                </div>

                <div className={styles.modalFormBtn}>
                  <Button
                    text="ОТПРАВИТЬ"
                    disabled={!isActive}
                    className={!isActive ? styles.isDisabled : ""}
                    onClick={() => console.log("request is sent")}
                  />
                </div>
              </form>
            </div>
          </div>
        </div>
      </Fragment>
    )
  );
};

const mapStateToProps = (state) => {
  return {
    isOpen: state.connectModal.isOpen,
    error: state.connectModal.error,
  };
};

export default connect(mapStateToProps, { closeModal })(ConnectModal);
