import React from 'react'

import styles from './SolutionsCard.module.scss'

const SolutionsCard = ({ header, text, image, className }) => {

    const cls = [
        styles.SolutionsCard,
        className
    ]

    return (
        <div className={cls.join(' ')}>
            <div className={styles.imageWrap}>
                <img className={styles.image} src={image} alt="SolutionsCardImg"/>
            </div>
            <div className={styles.SolutionsCardText}>
                {header && <h2>{header}</h2>}
               {text && (
                <p>
                    {text}
                </p>
               )}
            </div>

            

        </div>
    )
}

export default SolutionsCard