import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './OrderPaymentScheme.module.scss'
import img1 from '../../../img/developers/payment-scheme/payment-scheme-img1.png'
import img2 from '../../../img/developers/payment-scheme/payment-scheme-img2.png'
import img3 from '../../../img/developers/payment-scheme/payment-scheme-img3.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const OrderPaymentScheme = (props) => {

    return (
        <div className={styles.OrderPaymentScheme}>
           <AdaptiveDevBlock>
           <h1>Схема "Оплата заказа"</h1>
				<AdaptiveImg image={img1} />
				<p>
 <em>Схема Оплата заказа через Интернет-эквайринг</em>
				</p>
				<p>
 <br/>
					 1. Пользователь выбирает товар на сайте продавца.
				</p>
				<p>
					 2. Пользователь нажимает кнопку оплатить. В пользовательском интерфейсе открывается виджет с реквизитами для оплаты.
				</p>
				<p>
					 Пользователь вводит данные карты. Нажимает оплатить.
				</p>
				<p>
					 3. На стороне платёжной платформы выполняется регистрация платежа.
				</p>
				<p>
					 4. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты, вводит код подтверждения из sms.
				</p>
				<p>
					 5. После проверки карты с нее списывается сумма на счет Vepay.
				</p>
				<p>
					 6 В платёжную платформу поступает информация о статусе платежа.
				</p>
				<p>
					 7. Пользователю открывается виджет со статусом оплаты на странице Vepay или на странице веб-сервиса, к которой выполняется перенаправление.
				</p>
				<p>
					 Продавцу приходит колбэк о статусе оплаты.
				</p>
				<p>
 <br/>
					 Пользовательский сценарий:
				</p>
				<p>
					 Пользователь выбирает товар, нажимает оплатить. Открывается виджет для оплаты.
				</p>
				<p>
					 1. На форме для оплаты указаны: название сервиса, где клиент совершал заказ, сумма, которую он собирается оплатить и комиссия
				</p>
				<p>
					 2. Клиент указывает данные карты и оплачивает заказ
				</p>
				<p>
					 После отправки суммы на стороне платёжной платформы выполняется регистрация платежа.
				</p>
				<p>
					 3. Пользователь перенаправляется на страницу банка-эмитента для подтверждения оплаты, вводит код подтверждения из sms (рис.3)
				</p>
				<p>
					 После проверки карты с нее списывается сумма на счет Vepay.
				</p>
				<p>
					 4. Пользователю открывается виджет со статусом оплаты на странице Vepay или на странице веб-сервиса, к которой выполняется перенаправление
				</p>
				<AdaptiveImg image={img2} />
				<p>
					 5. После успешной оплаты, на email клиента приходит электронный чек с данными по оплате.
				</p>
				<AdaptiveImg image={img3} />
				<p>
					 &nbsp;
				</p>
				<h2>Пример API запроса оплаты товара</h2>
				<p>
					 Домен для запросов - <a href="https://api.vepay.online">https://api.vepay.online</a>
				</p>
				<p>
					 Пример: осуществляется перевод средств с карты клиента на счет магазина
				</p>
				<p>
					 /merchant/pay
				</p>
				 <pre><code>
{`{
    "amount": 100,
    "extid": "101464",
    "descript": "Заказ 1",
    "timeout": 10,
    "successurl": "https://exemple.com/successurl",
    "failurl": "https://exemple.com/failurl",
    "cancelurl": "https://exemple.com/cancelurl",
    "type": 0,
    "regcard": 1
}`}
</code>
</pre>
				<p>
					 Ответ:
				</p>
				 <pre><code>
{`{
    "status": 1,
    "message": "",
    "id": 101455,
    "url": "https://test.vepay.online/pay/form/101302"
}`}
</code>
</pre>
				<p>
					 Пользователь переходит по ссылке и перенаправляется на форму для оплаты заказа (виджет Vepay).
				</p>
				<p>
					 Параметры запроса
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 amount
					</td>
					<td>
						 Cумма платежа, руб
					</td>
				</tr>
				<tr>
					<td>
						 extid
					</td>
					<td>
						 Внешний идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 descript
					</td>
					<td>
						 Описание платежа
					</td>
				</tr>
				<tr>
					<td>
						 timeout
					</td>
					<td>
						 Тайм-аут ожидания оплаты, от 10 до 60 минут
					</td>
				</tr>
				<tr>
					<td>
						 successurl
					</td>
					<td>
						 Адрес Url для возврата после завершения платежа (успех)
					</td>
				</tr>
				<tr>
					<td>
						 failurl
					</td>
					<td>
						 Адрес Url для возврата после завершения платежа (ошибка)
					</td>
				</tr>
				<tr>
					<td>
						 cancelurl
					</td>
					<td>
						 Адрес Url для возврата после отказа от оплаты
					</td>
				</tr>
				<tr>
					<td>
						 type
					</td>
					<td>
						 (опционально) Тип шлюза - 0 – ECOM,1 – ЖКХ
					</td>
				</tr>
				<tr>
					<td>
						 regcard
					</td>
					<td>
						 (опционально) 1 – регистрация карты после платежа
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Статус (0 – ошибка 1 - принят)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Сообщение ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 url
					</td>
					<td>
						 Cсылка для перехода на форму оплаты
					</td>
				</tr>
				</tbody>
				</table>








           </AdaptiveDevBlock>
        </div>
    )
}

export default OrderPaymentScheme