import React from 'react'

import styles from './TextWithCircle.module.scss'

const TextWithCircle = ({ text, type = 'violet', className }) => {

  const cls = [
    styles.TextWithCircle,
    styles[type],
    className
]

    return (
        <h3 className={cls.join(' ')}>
             {text}
        </h3>
    )
}

export default TextWithCircle