import React from 'react'

import styles from './HowToPayStep.module.scss'

const HowToPayStep = ({className, number, text, clickHander, active, ...props}) => {


    const cls = [
        styles.HowToPayStep,
        className,
        active && styles.active
    ]

    return (
        <div className={cls.join(' ')} onClick={clickHander}>
                <div className={styles.number}>
                    <span>{number}</span>
                </div>
                <div  className={styles.text}>
                    <p>{text}</p>
                </div>
        </div>
    )
}

export default HowToPayStep