import React, { useEffect, useRef, useState }  from 'react'
import { CSSTransition } from 'react-transition-group';
import NavLinkPurple from '../NavLinkPurple/NavLinkPurple'

import styles from './DropDown.module.scss'

const DropDownBody = ({bodyCls, setShow, links, forwardedRef}) => {


  useEffect(() => {
    const closeDropDown = () => {
      setShow(false)
    }
    document.addEventListener('click', closeDropDown);
    return () => {
      document.removeEventListener('click', closeDropDown);
    };
    // eslint-disable-next-line 
  }, []);
  return (
    <div ref={forwardedRef} className={bodyCls.join(' ')} onClick={() => setShow(false)}>
      {
        links.map(({title, link}, id) => {
              return (
                  <NavLinkPurple className={styles.link} to={link} key={id}>
                      {title}
                  </NavLinkPurple>
              )
          })
      }
    </div>
  )
}

const DropDown = ({links=[], className, title, titleClass, isHeader}) => {

  const dropDownRef = useRef()

  const [show, setShow] = useState(false)

  const cls = [
    styles.DropDown,
    className
  ]

  const bodyCls = [
    styles.dropDownBody,
    isHeader? styles.headerDropDown : styles.sideMenuDropDown,
  ]

  const titleCls = [
    styles.title,
    titleClass,
    show? styles.arrowUp : ''
  ]

    return (
        <div className={cls.join(' ')}>
           <span 
              className={titleCls.join(' ')} 
              onClick={() => setShow(!show)}
           >
            {title} 
           </span>
           <CSSTransition in={show} timeout={200} classNames="contact-modal" unmountOnExit nodeRef={dropDownRef} >
                    <DropDownBody 
                        bodyCls={bodyCls} 
                        setShow={setShow} 
                        links={links}
                        forwardedRef={dropDownRef}
                    />
           </CSSTransition>
           {/* {
             show && <DropDownBody 
                        bodyCls={bodyCls} 
                        setShow={setShow} 
                        links={links}
                    />
           } */}
            {/* <div className={bodyCls.join(' ')} onClick={() => setShow(false)}>
              {
                links.map(({title, link}, id) => {
                      return (
                          <NavLinkPurple className={styles.link} to={link} key={id}>
                              {title}
                          </NavLinkPurple>
                      )
                  })
              }
            </div> */}
        </div>
    )
}

export default DropDown