import React from 'react'

import styles from './Contacts.module.scss'
import contactsTopImg from '../../img/contacts/contactsTopImg.png'
import vapaySvg from '../../img/Vepay-black.svg'
import { Map, Placemark, YMaps } from 'react-yandex-maps'


const Contacts = (props) => {

  const coordinates = [
    [55.758929, 37.664464],
  ];

  const placeMarkProps = {
    hintContent: 'Vepay',
    // iconContent: '120000000000000000000000',
    iconLayout: 'default#image',
    iconImageHref: vapaySvg,
    iconImageSize: [100, 100],
    iconImageOffset: [-65, -75],
    // iconContentOffset: [150, 150],
    // searchControlProvider: 'yandex#search',
  }

    return (
        <div className={styles.Contacts}>
            <div className={styles.contactsTopBlockWrap}>
              <img  className={styles.contactsTopImg} src={contactsTopImg} alt="contactsTopImg"/>
              <div className={styles.contactsTopBlock}>
                  <div className={styles.centered}>
                    <div  className={styles.blackBox}>
                      <h1>Контакты</h1>
                      <h3>105064, Москва, Нижний Сусальный переулок, 5с18</h3>
                      <div className={styles.mailPhone}>
                        <div className={styles.mailPhoneItem}>
                          <p>По общим вопросам</p>
                          <h3>info@vepay.online</h3>
                        </div>
                        <div>
                          <p>Контактный телефон</p>
                          <a href="tel:+7 499 954-84-95"><h3>+7 499 954-84-95</h3></a>
                        </div>
                      </div>
                    </div>
                    <div className={styles.support}>
                      <p>Техническая поддержка</p>
                      <h3>support@vepay.online</h3>
                    </div>
                  </div>
              </div>

              
            </div>

            <div className={styles.map}>
                  <YMaps>
                    <div className={styles.mapContent}>
                      {/* My awesome application with maps! */}
                      <Map 
                        defaultState={{ 
                          center: [55.758929, 37.664464], 
                          zoom: 16,
                          }} 
                        width={`100`} 
                        height={700} 
                      >
                          {coordinates.map((coordinate, i) => <Placemark  options={placeMarkProps} properties={placeMarkProps}  geometry={coordinate} key={i} />)}
                      </Map>
                    </div>
                  </YMaps>
              </div>

            
          
        </div>
    )
}

export default Contacts