import React from 'react'

import styles from './ContactInfo.module.scss'

const ContactInfo = ({type='footer'}) => {


    return (
        <div className={type === 'footer'? styles.ContactInfoFooter: styles.ContactInfo}>
            <div className={styles.contactsBlock}>
                <p>Техническая поддержка</p>
                <h2>info@vepay.online</h2>
            </div>

            <div className={styles.contactsBlock}>
                <p>Контактный телефон</p>
                <a href="tel:+7 499 954-84-95"> <h2>+7 499 954-84-95</h2></a>
               
            </div>
        </div>
    )
}

export default ContactInfo