import { CLOSE_MENU, OPEN_MENU } from "../actions/actionTypes";

const initialState = {
    menuIsOpened: false,
}


export default function layoutReducer(state = initialState, action) {
    switch (action.type) {
        case OPEN_MENU:
            return { ...state, menuIsOpened: true }
        case CLOSE_MENU:
            return { ...state, menuIsOpened: false }
        default:
            return state;
    }

}