import { GET_NEWS_FAILURE, GET_NEWS_SUCCESS, NEWS_LOADING,
  GET_NEWS_DETAILS,
} from "../actions/actionTypes";

const initialState = {
  news: [],
  loading: false,
  errors: null,
  details: null
}

export default function blogReducer(state = initialState, action) {
  switch (action.type) {
    case GET_NEWS_SUCCESS:
      return {
        ...state,
        news: action.news
      }
      case NEWS_LOADING:
      return {
        ...state,
        loading: action.status
      }
      case GET_NEWS_FAILURE:
      return {
        ...state,
        errors: action.errors
      }
      case GET_NEWS_DETAILS:
        return {
          ...state,
          details: action.details
        }
    default:
      return state;
  }
}