import React from 'react'

import styles from './ApplePay.module.scss'
import img1 from '../../../img/developers/apple-pay/apple-pay.png'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const ApplePay = (props) => {

    return (
        <div className={styles.ApplePay}>
           <AdaptiveDevBlock>
            <h1>Apple Pay</h1>
            <p>
            VEPAY поддерживает Apple Pay — платёжный метод для оплаты товаров и услуг в приложениях iOS и на сайтах. Любой платеж подтверждается на телефоне с помощью Touch ID или Face ID. Деньги же списываются с банковской карты, которая привязана к кошельку Apple. Для работы с Apple Pay необходимо иметь сертификат мерчанта и сайта.
            </p>
            <p>
            Для реализации возможности оплаты с использованием Apple Pay на сайте необходимо зарегистрировать или иметь Apple Merchant ID, сформировать/использовать уже имеющийся платежный сертификат, а также подтвердить в Apple владение доменами, на которых будет реализована оплата. Все это делает мерчант на сайте apple.com самостоятельно.
            </p>
            <p>
            Для включения возможности использования Apple Pay необходимо передать VEPAY платежный сертификат.
            </p>
            <AdaptiveImg image={img1} />







           </AdaptiveDevBlock>
        </div>
    )
}

export default ApplePay