import React, { useState } from 'react'
import { connect } from 'react-redux'
import Button from '../../../components/UI/Button/Button'
import TextWithTick from '../../../components/UI/TextWithTick/TextWithTick'

import styles from './SelfEmployedTariffs.module.scss'
import { openModal } from '../../../redux/actions/connectModalActions'

const SelfEmployedTariffs = ({openModal}) => {

    const [open, setOpen] = useState(false)

    const toggleModals = () => {
        setOpen(false)
        openModal()
    }

    // const openTariffsModal = () => {
    //     setOpen(true)
    // }

    // const showContactUsModal = () => {
    //     console.log('click Выплаты');
    // }

    const tariffsList1 = [
        {
            text: 'Банковские карты Visa, Mastercard, Maestro, МИР',
            type: 'purple'
        },
        {
            text: 'Банковские счета в России',
            type: 'purple'
        },
        {
            text: 'Электронные кошельки Qiwi',
            type: 'purple'
        },
        {
            text: 'Бесплатное подключение',
            type: 'purple'
        },
        {
            text: 'Без абонентской платы',
            type: 'purple'
        },

    ]

    const tariffsList2 = [
        {
            text: 'Банковские карты Visa, Mastercard, Maestro, МИР',
            type: 'green'
        },
        {
            text: '1% на определённые категории',
            type: 'green'
        },
        {
            text: 'Бесплатное подключение',
            type: 'green'
        },
        {
            text: 'Без абонентской платы',
            type: 'green'
        },
    ]


    return (
        <div className={styles.SelfEmployedTariffs}>
            <span
                className={styles.link}
                onClick={() => setOpen(true)}
            >тарифы <span  className={styles.linkArrow}>&#8594;</span> </span>

            {
                open && (
                    <div className={styles.modal}>

                        <span
                            className={styles.modalCloseButton}
                            onClick={() => setOpen(false)}
                        >&times;</span>

                        <h1>Тарифы</h1>
                        <div className={styles.modalBody}>
                            <div className={styles.modalBodyItem}>
                                <Button
                                    text={'Выплаты'}
                                    type={'violetBG'}
                                    clickHander={toggleModals}
                                />

                                <p>
                                    <span className={styles.percent}>2,5%</span><span className={styles.grayText}>за транзакцию</span>
                                </p>

                                {

                                    tariffsList1.map(item => (
                                        <TextWithTick
                                            className={styles.tickedText}
                                            {...item}
                                            key={item.text}
                                        />))

                                }

                                <p>
                                Комиссия может меняться для определенных объемов оборота и видов деятельности. Для некоторых платежных систем есть фиксированная стоимость за каждую транзакцию.
                                </p>

                            </div>

                            <div className={styles.modalBodyItem}>
                                <Button
                                    text={'Приём платежей'}
                                    type={'green'}
                                    clickHander={toggleModals}
                                />

                                <p>
                                    <span className={styles.percent}>3,5%</span><span className={styles.grayText}>за транзакцию</span>
                                </p>
                                    {

                                        tariffsList2.map(item => (
                                            <TextWithTick
                                                className={styles.tickedText}
                                                {...item}
                                                key={item.text}
                                            />))

                                    }
                               
                                <p>
                                Комиссия может меняться для определенных объемов оборота и видов деятельности. Для оборотов свыше 5 млн рублей в месяц предложим индивидуальные условия
                                </p>

                            </div>

                        </div>
                    </div>
                )
            }

        </div>
    )
}


export default connect(null, {openModal})(SelfEmployedTariffs)