import React from 'react'

import styles from './Capabilities.module.scss'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'
import img1 from '../../../img/developers/capabilities/capabilities-img1.png'
import img2 from '../../../img/developers/capabilities/capabilities-img2.png'
import img3 from '../../../img/developers/capabilities/capabilities-img3.png'

const Capabilities = (props) => {

    return (
        <div className={styles.Capabilities}>
        <AdaptiveDevBlock>
            <h1>Поддержка разных целевых действий.</h1>
            <p>
            Vepay поддерживает:
            </p>
            <h3>Интернет-эквайринг.</h3>
            <p>
            Банковская услуга, заключающаяся в приеме платежей и переводе средств продавцу посредством использования платежных карт. Это наиболее распространённый вариант использования платёжной формы — с проведением оплат в одну стадию, при которых в рамках одного сеанса работы Vepay осуществляется разовый перевод денежных средств от пользователя к продавцу, например за совершаемую покупку.
            </p>
            <h3>
            Холдирование средств.
            </h3>
            <p>
            При таком варианте использования в рамках сеанса работы Vepay осуществляется блокировка денежных средств на счёте пользователя, и продавец получает возможность уже в дальнейшем списать нужную сумму или вернуть деньги пользователю. Имея данную возможность, не нужно оформлять возврат, достаточно просто не списывать замороженные средства.
            </p>
            <h3>Регулярные платежи.</h3>
            <p>
            Данная возможность дает компаниям списывать средства с банковской карты клиента без дополнительного подтверждения каждой транзакции (безакцептные платежи). Рекарринговые платежи экономят время клиента — достаточно один раз указать данные банковской карты и платежи будут проходить автоматически. Это осуществляется с помощью получения токена карты, который формируется в платежном шлюзе и передается продавцу для последующих автоплатежей.
            </p>
            <h3>Регистрация карты на платёжном шлюзе (привязка).</h3>
            <p>
            Данная операция используется для регистрации (привязки) карты стороннего Банка в системе платёжного шлюза. Данный метод используется в рамках осуществления списания с карт сторонних Банков, в том числе, рекуррентных платежей, проверки принадлежности ее клиенту. При регистрации карты, с нее списывается и сразу возвращается определенная сумма, указанная продавцом. клиенту при вводе данных на форме сумма не показывается. После проведения операции клиент может быть возвращен на сайт продавца, адрес которого передается в запросе. На сайте продавца клиенту необходимо ввести сумму, списанную с карты. Если сумма совпадет с той, что была передана в начале операции - продавец в своей системе помечает карту как привязанную.
            </p>
            <h3>
            Callback Service.
            </h3>
            <p>
            Для получения статуса по осуществлению операций по картам через платёжный шлюз, можно использовать механизм оповещений.
            </p>
            <p>
            Во-первых, программные оповещения, которые автоматически отправляются на заданные URL по итогам выполнения целевых действий и содержат детальную информацию о результатах. Через эти оповещения можно обеспечивать оперативный автоматический контроль ситуации на стороне серверной части веб-сервиса.
В-вторых, средства интерфейса Личного Кабинета, с помощью которого можно обеспечивать контроль и анализ ситуации сотрудниками, как по отдельным платежам, так и по сводным показателям.
            </p>
            <h1>
            Функциональность платёжной формы (виджета).
            </h1>
            <h3>Способы вызова формы.</h3>
            <p>
            Вызов Vepay из веб-сервиса может быть реализован как через переход по кнопке, так и через другие интерфейсные события. В подключаемой JavaScript-библиотеке для этого доступны соответствующие методы, а если вызов Vepay осуществляется через API, то со стороны контрагента можно использовать необходимую функциональность непосредственно в веб-сервисе.
            </p>
            <h3>
            Способы отображения формы.
            </h3>
            <p>
            Vepay поддерживает следующие варианты отображения:
            </p>
            <ul>
                <li>
                в виде отдельной HTML-страницы, в текущей или новой вкладке браузера.
                </li>
                <li>
                в модальном окне поверх HTML-страницы
                </li>
                <li>
                в объекте iframe HTML-страницы
                </li>
            </ul>
            <p>
            Два последних пункта зависят от настроек сервера контрагента. Клиент сам указывает с каких сайтов можно использовать iframe для открытия страниц.
            </p>
            <AdaptiveImg
                image={img1}
            />
            <p>
            Виджет Vepay в модальном окне поверх HTML-страницы
            </p>
            <AdaptiveImg image={img2} />
            <p>Вызов Vepay виджета в объекте iframe HTML-страницы</p>
            <AdaptiveImg image={img3} />
            <p>
            Виджет Vepay в виде отдельной HTML-страницы, в текущей или новой вкладке браузера
            </p>
            <br/>
            <h3>Способы перенаправлений с формы.</h3>
            <p>
            При выполнении целевых действий через Vepay порой необходимы перенаправления пользователя к сторонним сервисам — например, для аутентификации 3-D Secure или подтверждения оплаты на стороне платёжной системы. Для таких перенаправлений могут использоваться варианты с отдельной HTML-страницей (в текущей или новой вкладке браузера) и с объектом iframe в HTML-странице. Конкретный вариант настраивается на стороне конрагента.
            </p>
            <h3>Варианты оформления формы.</h3>
            <p>
            Для Vepay можно использовать как типовое оформление, так и индивидуальное — в соответствии с предпочтениями контрагента. Во втором случае менять вид формы можно очень существенно: в плане визуальных элементов (логотипов и иконок), шрифтовых гарнитур.
            </p>
            <h3>
            Предоставление повторных попыток ввода данных.
            </h3>
            <p>
                При использовании этой функциональности в случае ошибки при выполнении ввода реквизитов карты отображается соответствующее сообщение (например, неправильно указан номер карты) и предложение повторить попытку.
            </p>












        </AdaptiveDevBlock>
           



        </div>
    )
}

export default Capabilities