import React from 'react'
import DropDown from '../UI/DropDown/DropDown'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'

import styles from './NavMenu.module.scss'

const NavMenu = ({ isHeader }) => {
   
   const productsList = [
    { title: 'Приём платежей', link: '/receiving-payments' },
    { title: 'Выплаты', link: '/payouts' },
    { title: 'Отраслевые решения', link: '/industry-solutions' },
    { title: 'Онлайн-кредит и рассрочка', link: '/credits' },
    { title: 'Интеграция', link: '/developers/connection' },
   ] 

    return (
        <ul className={isHeader ? styles.header : styles.sidebar}>
            <li className={styles.item}>
                <DropDown
                    links={productsList}
                    title={'Самозанятым'}
                    titleClass={styles.link}
                    isHeader={isHeader}
                />
            </li>
            <li className={styles.item}>
                <DropDown
                    links={productsList}
                    title={'Юр лицам'}
                    titleClass={styles.link}
                    isHeader={isHeader}
                />
            </li>
 
            <li className={styles.item}>
                <NavLinkPurple to="/developers" className={styles.link}>Разработчикам</NavLinkPurple>
            </li>
       

        </ul>

    )
}

export default NavMenu