import React from 'react'

import styles from './ClientConnection.module.scss'
import img1 from '../../../img/developers/client-connection/client-connection-img1.png'
import img2 from '../../../img/developers/client-connection/client-connection-img2.png'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const ClientConnection = (props) => {

    return (
        <div className={styles.ClientConnection}>
           <AdaptiveDevBlock>
            <h1>Схема подключения клиента</h1>
            <p>
            Подключение к платежной системе VEPAY занимает 3 рабочих дня после предоставления полного пакета документов.
            </p>
            <p>
            При работе с Vepay взаимодействие партнера с платёжной платформой выполняется через личный кабинет, который используется с соответствующими правами доступа.
            </p>
            <p>
            Взаимодействие с базой данных сервиса реализуется через прикладной протокол SQL и транспортный протокол TCP/IP.
            </p>
            <AdaptiveImg image={img1} />
            <h3>Схема создания тестового доступа</h3>
            <p>
            1. Заявка клиента на сайте приходит на sales@vepay.online либо менеджеру в результате холодного звонка.
            </p>
            <p>
            2. Департамент продаж обрабатывает заявку.
            </p>
            <p>
            3. Переговоры с клиентом (по почте, в исключительных случаях по Skype), уточнение необходимых данных.
            </p>
            <p>
            4. Направление клиенту договора и документов для подготовки и подписания, ожидания сканов от него.
            </p>
            <p>
            5. Передача обработанной заявки в ИТ-департамент на it@vepay.online для создания тестового доступа.
            </p>
            <p>
            6. Создание учетной записи и доступов в ЛК.
            </p>
            <p>
            7. Выбираем контрагенты → Регистрация. Ставим галочку "является МФО". Регистрируем.
            </p>
            <p>
            8. Вкладка "Данные" - заполнение общих данных для входа в ЛК.
            </p>
            <p>
            9. Вкладка "Услуги". Редактирование данных по видам услуг и комиссиям, если нужно.
            </p>
            <p>
            10. Вкладка "Настройки":
            </p>
            <p>
            а) заполнение тестовых терминалов (T1653100822ID);
            </p>
            <p>
            б) генерация ключа к IP, сохранение в нужном поле;
            </p>
            <p>
            в) заполнение остальных данных.
            </p>
            <AdaptiveImg image={img2} />
            <h3>Схема создания боевого доступа</h3>
            <p>
            1. Заявка клиента на сайте приходит на sales@vepay.online либо менеджеру в результате холодного звонка.
            </p>
            <p>
            2. Департамент продаж обрабатывает заявку.
            </p>
            <p>
            3. Переговоры с клиентом (по почте, в исключительных случаях по Skype), уточнение необходимых данных.
            </p>
            <p>
            4. Направление клиенту договора и документов для подготовки и подписания, ожидания сканов от него.
            </p>
            <p>
            5. Передача обработанной заявки в ИТ-департамент на it@vepay.online для создания тестового доступа (см. Схема создания тестового доступа)
            </p>
            <p>
            6. Направление комплекта документов в банк для согласования и регистрации. Ожидание положительного решения от банка.
            </p>
            <p>
            7. Передача на it@vapay.online заявления с данными по клиенту для создания боевого доступа.
            </p>
            <p>
            8. Получение от банка контейнера и пароля для подключения к шлюзам.
            </p>
            <p>
            9. Получение банковской информации для движения д/c (номер счета, комиссии, способ вывода комиссии Vepay).
            </p>











           </AdaptiveDevBlock>
        </div>
    )
}

export default ClientConnection