import React from 'react'
import { Route, Switch } from 'react-router-dom'
import Tariffs from '../../pages/Tariffs/Tariffs'
import FZ54 from '../../pages/FZ54/FZ54'
import Developers from '../../pages/Developers/Developers'
import Partners from '../../pages/Partners/Partners'

import styles from './Main.module.scss'
import HomePage from '../../pages/HomePage/HomePage'
import NotFound from '../../pages/NotFound/NotFound'
import SelfEmployed from '../../pages/SelfEmployed/SelfEmployed'
import LegalEntity from '../../pages/LegalEntity/LegalEntity'
import AboutUs from '../../pages/AboutUs/AboutUs'
import ReceivingPayments from '../../pages/ReceivingPayments/ReceivingPayments'
import Payouts from '../../pages/Payouts/Payouts'
import Credits from '../../pages/Credits/Credits'
import Blog from '../../pages/Blog/Blog'
import IndustrySolutions from '../../pages/IndustrySolutions/IndustrySolutions'
import MFO from '../../pages/IndustrySolutions/MFO/MFO'
import CrowdFunding from '../../pages/IndustrySolutions/CrowdFunding/CrowdFunding'
import InternetShops from '../../pages/IndustrySolutions/InternetShops/InternetShops'
import OnlineStudy from '../../pages/IndustrySolutions/OnlineStudy/OnlineStudy'
import Housing from '../../pages/IndustrySolutions/Housing/Housing'
import Contacts from '../../pages/Contacts/Contacts'
import Marketing from '../../pages/Marketing/Marketing'
import Help from '../../pages/Help/Help'
import SystemRules from '../../pages/SystemRules/SystemRules'
import Feedback from '../../pages/Feedback/Feedback'
import NewsDetails from '../../pages/Blog/NewsDetails/NewsDetails'
import Vacancies from '../../pages/Vacancies/Vacancies'
// import NewsCreation from '../../pages/Blog/NewsCreation/NewsCreation'

const Main = (props) => {

    return (
        <div className={styles.Main}>
            <Switch>
                <Route path="/" exact component={HomePage}/>
                <Route path="/tariffs" component={Tariffs} />
                <Route path="/54-fz" component={FZ54} />
                <Route path="/partners" component={Partners} />
                <Route path="/self-employed" component={SelfEmployed} />
                <Route path="/legal-entity" component={LegalEntity} />
                <Route path="/about-us" component={AboutUs} />
                <Route path="/receiving-payments" component={ReceivingPayments} />
                <Route path="/payouts" component={Payouts} />
                <Route path="/credits" component={Credits} />
                <Route path="/blog" exact component={Blog} />
                <Route path="/blog/:id" component={NewsDetails} />
                {/* <Route path="/news-creation" component={NewsCreation} /> */}
                <Route path="/industry-solutions" exact component={IndustrySolutions} />
                <Route path="/industry-solutions/mfo" component={MFO} />
                <Route path="/industry-solutions/crowd-funding" component={CrowdFunding} />
                <Route path="/industry-solutions/internet-shops" component={InternetShops} />
                <Route path="/industry-solutions/online-study" component={OnlineStudy} />
                <Route path="/industry-solutions/housing" component={Housing} />
                <Route path="/contacts" component={Contacts} />
                <Route path="/marketing" component={Marketing} />
                <Route path="/help" component={Help} />
                <Route path="/system-rules" component={SystemRules} />
                <Route path="/feedback" component={Feedback} />
                <Route path="/developers" component={Developers} />
                <Route path="/vacancies" component={Vacancies} />

                <Route component={NotFound} />
            </Switch>
        </div>
    )
}

export default Main