import React from 'react'

import styles from './AboutUs.module.scss'
import topImg from '../../img/about-us/aboutUsTopImg.png'
import topImg2 from '../../img/about-us/aboutUsTopImg2.png'
import missionImg1 from '../../img/about-us/missionImg1.svg'
import missionImg2 from '../../img/about-us/missionImg2.svg'
import missionImg3 from '../../img/about-us/missionImg3.svg'
import missionImg4 from '../../img/about-us/missionImg4.svg'
import missionImg5 from '../../img/about-us/missionImg5.svg'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'

const AboutUs = (props) => {

    const missionsList = [
        {
            header: 'МЫ ВЫСОКОТЕХНОЛОГИЧНЫ',
            image: missionImg1,
            text: 'Собственные разработки позволяют нам быть на шаг впереди и опережать время'
        },
        {
            header: 'МЫ КОММУНИКАТИВНЫ',
            image: missionImg3,
            text: 'Ценим наших Клиентов и готовы к диалогу 24/7 для достижения наилучшего результата'
        },
        {
            header: 'МЫ ВАРИАТИВНЫ',
            image: missionImg5,
            text: 'Работа с нами означает получение множества решений для разного типа бизнеса'
        },
        {
            header: 'МЫ НАДЕЖНЫ',
            image: missionImg2,
            text: 'Держим качество предоставляемого сервиса на высочайшем уровне'
        },
        {
            header: 'МЫ ТРАДИЦИОННЫ',
            image: missionImg4,
            text: 'Нам важны долгосрочные отношения. Каждый клиент для нас ценен и уникален'
        },
    ]

    const topBlockItemText2 = (
        <span>
        Работаем для вас с 2010 года. За время своего существования компания VEPAY прошла путь от приема платежей до полноценного процессингового центра, предоставляющего решения в области платежных технологий.
        <br/> <br/>
        Основным направлением работы VEPAY являются отрасли МФО и финансовые организации, ЖКХ, интернет-магазины и маркетплейс, краудфандинг-платформы, сервисы онлайн-образования.
         </span>
        

    )

    return (
        <div className={styles.AboutUs}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImg2}
                topBlockItemText1={'О Нас'}
                topBlockItemText2={topBlockItemText2}
            />

            <ColouredBGBlock>
                <MaxWidthBlock>
                    <div  className={styles.missionsBlockWrap}>
                        <center><h1>Миссия VEPAY</h1></center>
                        <div className={styles.missionsBlock}>
                            {
                                missionsList.map(item => <SolutionsCard {...item} key={item.header}/>)
                            }
                        </div>
                    </div>

                    {/* <div className={styles.ourTeamBlock}>
                        <h1>Наша команда</h1>
                    
                            <div className={styles.imgBlock1}>
                                <img src={kolobov} alt="Kolobov"/>
                                <h1>ЕВГЕНИЙ КОЛОБОВ</h1>
                                <span>Генеральный директор</span>
                            </div>
                            <div className={styles.imgBlock2}>
                                <img src={malinina} alt="Malinina"/>
                                <h1>ТАТЬЯНА МАЛИНИНА</h1>
                                <span>Директор по маркетингу</span>
                            </div>
                            <div className={styles.imgBlock3}>
                                <img src={ozerov} alt="Ozerov"/>
                                <h1>ВАЛЕНТИН ОЗЕРОВ</h1>
                                <span>Директор по IT</span>
                            </div>
                    
                    </div> */}
                </MaxWidthBlock>
            </ColouredBGBlock>

           
        </div>
    )
}

export default AboutUs