import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './RecurringPayments.module.scss'
import img1 from '../../../img/developers/recurring/recurring-img.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'
import { Link } from 'react-router-dom'

const RecurringPayments = (props) => {

    return (
        <div className={styles.RecurringPayments}>
           <AdaptiveDevBlock>
           <h1>Рекарринг и периодические платежи</h1>
				<p>
					 Для сервисов Vepay существует система периодических платежей.
				</p>
				<p>
					 Если у Вас есть услуги, которыми регулярно пользуются ваши клиенты, вы можете автоматически списывать денежные средства с банковских карт, что может существенно облегчить процесс оплаты и повысить лояльность ваших клиентов.
				</p>
				<p>
					 Это дает возможность:
				</p>
				<ul>
					<li>совершать оплату с помощью банковской карты «в один клик» без необходимости ввода платежных реквизитов.</li>
					<li>выполнять регулярные списания денежных средств без участия плательщика для инициации очередного платежа.</li>
					<li>запланировать режим рекуррентных списаний - когда происходит просрочка, клиенту приходит запрос на списание денежных средств со счета.</li>
				</ul>
				<p>
					 Для подключения рекарринга клиент должен совершить первый тестовый платеж, после чего данные его карты сохраняются в системе.
				</p>
				<p>
					 Клиент при намерении оплатить товар или услугу на вашем сайте, соглашается с Вашими условиями по автоматическому списанию средств со счета его банковской карты.
				</p>
				<p>
					 Схема подключения периодических платежей аналогична схеме в разделе "<Link to="/developers/card-registration">Регистрация карты</Link>":
				</p>
				<p>
					 &nbsp;
				</p>
				<h2>Пример API сервиса Рекарринг</h2>
				<p>
					 Домен для запросов - <a href="https://api.vepay.online">https://api.vepay.online</a>
				</p>
				<p>
					 Пример:<br/>
					 1. Регистрация карты - отправляется запрос по API:
				</p>
				<p>
					 /recarring/reg<br/>
				</p>
				 <pre><code>
{`{
	"type": 0,
	"extid": "123e4567-e89b-12d3-a456-426655440000",
	"timeout": 10,
	"successurl": "https://example.org/success",
	"failurl": "https://example.org/fail",
	"cancelurl": "https://example.org/cancel",
}`}
</code>
</pre>
				<p>
					 Ответ:<br/>
				</p>
				 <pre><code>
{`{
	"status": 1,
	"id": 11021,
	"url": "https://test.vepay.online/pay/form/11021"
}`}
</code>
</pre>
				<p>
					 2. Клиент переходит на страницу тестового платежа (параметр url в ответе), где вводит данные карты, которая будет сохранена в системе.
				</p>
				<AdaptiveImg image={img1} />
				<p>
					 3. После завершения оплаты отправляется запрос по API на получение идентификатора карты:
				</p>

				<p>
					type=0 - карта для автоплатежа<br/>
					type=1 - карта для пополнения<br/>
				</p>
				<p>
					 /recarring/get<br/>
				</p>
				 <pre><code>
{`{
	"id": 11234, 
	"type": 0
}`}
</code>
</pre>
				<p>
					 Ответ:<br/>
				</p>
				 <pre><code>
{`{
	"status": 1,
	"card":  {
	  "id": 72,
	  "num": "411111******1111",
	  "exp": "12/19"
	}
}`}
</code>
</pre>
				<p>
					 Данные карты необходимо сохранить, чтобы использовать их для дальнейшей оплаты.
				</p>
				<p>
					 4. С помощью id карты можно совершить платеж:
				</p>
				<p>
					 /recarring/pay<br/>
				</p>
				 <pre><code>
{`{
	"card": 72,
	"amount": 10.00,
	"extid": "331"
}`}
</code>
</pre>
				<p>
					 Ответ:<br/>
				</p>
				 <pre><code>
{`{
	"status": 1,
	"id": 14954,
	"message": ""
}`}
</code>
</pre>
				<p>
					 5. После совершения платежа необходимо проверить его статус:
				</p>
				<p>
					 /merchant/state<br/>
				</p>
				 <pre><code>
{`{
	"id": 1234
}`}
</code>
</pre>
				<p>
					 Ответ:<br/>
				</p>
				 <pre><code>
{`{
	"status": 1,
	"message": "Успешно"
}`}</code></pre>











           </AdaptiveDevBlock>
        </div>
    )
}

export default RecurringPayments