import React, { useState } from 'react'

import styles from './Tariffs.module.scss'
import topImg from '../../img/tariffs/tariffsTopImg.png'
import topImg2 from '../../img/tariffs/tariffsTopImg2.png'
import internetShopImg from '../../img/tariffs/tariffIntShop.png'
import aviaImg from '../../img/tariffs/tariffAvia.png'
import housingImg from '../../img/tariffs/tariffHousing.png'
import CardWithButton from '../../components/Cards/CardWithButton/CardWithButton'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'

const Tariffs = (props) => {

    const [intShopValue, setIntShopValue] = useState('до 1 млнP')
    const [aviaValue, setAviaValue] = useState('до 1 млнP')
    const [housingValue, setHousingValue] = useState('до 1 млнP')

    const intShopPercent = {
        "до 1 млнP": '2,7 %',
        "от 1 млн до 5 млн": '2,6 %',
        "от 5 млн до 15 млн": '2,5 %',
        "от 15 млн до 30 млн": '2,3 %',
        "от 30 млн и более": 'По запросу'
    }

    const aviaPercent = {
        "до 1 млнP": '1,7 %',
        "от 1 млн до 5 млн": '1,6 %',
        "от 5 млн до 15 млн": '1,4 %',
        "от 15 млн до 30 млн": '1,2 %',
        "от 30 млн и более": 'По запросу'
    }

    const housingPercent = {
        "до 1 млнP": '1,5 %',
        "от 1 млн до 5 млн": '1,3 %',
        "от 5 млн до 15 млн": '1,1 %',
        "от 15 млн до 30 млн": '0,9 %',
        "от 30 млн и более": 'По запросу'
    }

    return (
        <div className={styles.Tariffs}>
            
           <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImg2}
                topBlockItemText1={'Тарифы'}
                topBlockItemText2={'Комиссия VEPAY за проведенные операции зависит от сферы деятельности компании, оборота и типа услуги.'}
           />

            <ColouredBGBlock>
                <div className={styles.tariffsBlock}>
                    <h1 className={styles.tariffsHeader}>Калькулятор комиссий VEPAY</h1>
                    <CardWithButton  header='Интернет-магазины' image={internetShopImg}
                    >
                        <p className={styles.cardText1}>оборот в месяц</p>
                        <select 
                            value={intShopValue} 
                            name="internet-shops" 
                            id="internet-shops"
                            onChange={(e) => setIntShopValue(e.target.value)}
                        >
                            <option value="до 1 млнP">до 1 млнP</option>
                            <option value="от 1 млн до 5 млн">от 1 млн до 5 млн</option>
                            <option value="от 5 млн до 15 млн">от 5 млн до 15 млн</option>
                            <option value="от 15 млн до 30 млн">от 15 млн до 30 млн</option>
                            <option value="от 30 млн и более">от 30 млн и более</option>
                        </select>
                        <p>
                            <small>размер комиссии VEPAY</small>
                        </p>
                        <h1>{intShopPercent[intShopValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header='Авиа'  image={aviaImg}>
                        <p className={styles.cardText1}>оборот в месяц</p>
                        <select 
                            value={aviaValue} 
                            name="avia" 
                            id="avia"
                            onChange={(e) => setAviaValue(e.target.value)}
                        >
                            <option value="до 1 млнP">до 1 млнP</option>
                            <option value="от 1 млн до 5 млн">от 1 млн до 5 млн</option>
                            <option value="от 5 млн до 15 млн">от 5 млн до 15 млн</option>
                            <option value="от 15 млн до 30 млн">от 15 млн до 30 млн</option>
                            <option value="от 30 млн и более">от 30 млн и более</option>
                        </select>
                        <p>
                            <small>размер комиссии VEPAY</small>
                        </p>
                        <h1>{aviaPercent[aviaValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header='ЖКХ'  image={housingImg}>
                        <p className={styles.cardText1}>оборот в месяц</p>
                        <select 
                            value={housingValue} 
                            name="housing" 
                            id="housing"
                            onChange={(e) => setHousingValue(e.target.value)}
                        >
                            <option value="до 1 млнP">до 1 млнP</option>
                            <option value="от 1 млн до 5 млн">от 1 млн до 5 млн</option>
                            <option value="от 5 млн до 15 млн">от 5 млн до 15 млн</option>
                            <option value="от 15 млн до 30 млн">от 15 млн до 30 млн</option>
                            <option value="от 30 млн и более">от 30 млн и более</option>
                        </select>
                        <p>
                            <small>размер комиссии VEPAY</small>
                        </p>
                        <h1>{housingPercent[housingValue]}</h1>
                        
                    </CardWithButton>
                </div>

                <div className={styles.bottomTextBlock}>
                    <p className={styles.bottomText}>
                         Принимая во внимание особенности различных организаций, мы всегда готовы предложить индивидуальное решение.
                    </p>
                 </div>
            </ColouredBGBlock>

            

        </div>
    )
}

export default Tariffs