import React from 'react'

import styles from './FZ54.module.scss'
import topImg from '../../img/fz-54/FZ54TopImg.png'
import topImg2 from '../../img/fz-54/FZ54TopImg2.png'
import cashierImg from '../../img/fz-54/online-cashier.png'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import BlackCard from '../../components/Cards/BlackCard/BlackCard'
import TextWithCircle from '../../components/UI/TextWithCircle/TextWithCircle'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'


const FZ54 = (props) => {

    const advantagesList = [
        'Договор с ОФД;',
        'Отправка чеков в ОФД;',
        'Статистика по кассам;',
        'Помощь при регистрации ККТ;',
        'Размещение в ЦОДе уровня Tier III;',
        'Работоспособность касс 24/7;',
    ]

    return (
        <div className={styles.FZ54}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImg2}
                topBlockItemText1={'Онлайн-касса'}
                topBlockItemText2={'Автоматизированное решение в соответствии с требованиями 54-ФЗ «О применении контрольно-кассовой техники» от 1 июля 2017 г. для платежей в Интернете.'}
            />

            <ColouredBGBlock>
                <MaxWidthBlock>

                    <div  className={styles.cashierBlock1}>
                        <BlackCard
                            header='Онлайн-касса'
                            buttonText={'подключить кассу'}
                            contentStyles={styles.contentStyles}
                        >
                            <TextWithCircle text={'Обеспечивает формирование фискальных документов о платежах в ОФД и ФНС в режиме реального времени;'} />
                            <TextWithCircle text={'Оснащена фискальным накопителем (ФН);'} />
                            <TextWithCircle text={'Подключена к сети Интернет;'} />
                            
                        </BlackCard>
                        <BlackCard
                            header='Оператор фискальных данных'
                            buttonText={'подключить кассу'}
                            contentStyles={styles.contentStyles}
                        >
                            <TextWithCircle 
                                text={'Ежедневно отправляет фискальные документы о проведенных платежах в ФНС;'}
                            />
                            <TextWithCircle 
                                text={'Обеспечивает хранение фискальных данных не менее пяти лет;'}
                            />
                            <TextWithCircle 
                                text={'Направляет электронный чек о проведенной операции плательщику;'}
                            />
                            <TextWithCircle 
                                text={'Заключение договора с ОФД обязательно.'}
                            />
                        </BlackCard>
                    </div>

                    <AdaptiveFlexBlock className={styles.cashierBlock2}>
                        <AdaptiveColumn header='ККТ NETPAY-ФС'>
                            <p className={styles.cashierText}>
                            Полноценная онлайн-касса, адаптированная к использованию именно интернет-магазинами, функционирует в автоматическом режиме круглосуточно, без необходимости ввода платежных данных вручную.
                            </p>
                        </AdaptiveColumn>
                        <AdaptiveColumn>
                            <img className={styles.cashierImg}  src={cashierImg} alt="cashierImg"/>
                        </AdaptiveColumn>
                        <AdaptiveColumn className={styles.cashierImgHidden}>
                            <img src={cashierImg} alt="cashierImg"/>
                        </AdaptiveColumn>
                        <AdaptiveColumn header='ПРЕИМУЩЕСТВА ORANGEDATA' headerStyles={styles.orangeData}>
                            {
                                advantagesList.map(item => <TextWithTick className={styles.tickedText} text={item} type='green' key={item}/>)
                            }
                        </AdaptiveColumn>
                    </AdaptiveFlexBlock>

                </MaxWidthBlock>
            </ColouredBGBlock>

          
        </div>
    )
}

export default FZ54