import React from 'react'

import styles from './CloseButton.module.scss'
import closeButton from '../../../img/close-button.svg'


const CloseButton = ({closeMenu}) => {

    return (
        <img
            className={styles.CloseButton}
            src={closeButton}
            alt="close-button"
            onClick={closeMenu}
        />
    )
}

export default CloseButton