import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './PersonalAccount.module.scss'
import img1 from '../../../img/developers/personal-account/personal-account-img1.png'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const PersonalAccount = (props) => {

    return (
        <div className={styles.PersonalAccount}>
            <AdaptiveDevBlock>
                <h1>Личный кабинет</h1>
                <p>
                Личный кабинет - это интерфейс, позволяющий отслеживать информацию о платежных операциях, формировать и выгружать отчеты, проводить аналитику, а также выполнять некоторые операции (например, возврат или выплата средств пользователю).
                </p>
                <p>
                Для авторизации в личном кабинете клиент вводит логин и пароль, выданный ему при регистрации. Доступ в Личный кабинет предоставляется сотрудниками службы технической поддержки.
                </p>
                <AdaptiveImg image={img1} />
                <p>
                В личном кабинете пользователю доступны:
                </p>
                <p>
                Главная страница.
                </p>
                <ul>
                    <li>
                    Первая страница при входе в кабинет. Содержит новости и контакты служб сервиса.
                    </li>
                </ul>
                <p>
                Баланс.
                </p>
                <ul>
                    <li>
                    Содержит информацию по текущим остаткам на счетах клиентов и партнеров, а также фильтр выбора клиента и периодом времени.
                    </li>
                </ul>
                <p>
                Отчеты.
                </p>
                <ul>
                    <li>
                    Платежи. Раздел содержит инструменты для отслеживания операций и подведения итогов за указанный временной период.
                    </li>
                    <li>
                    Отчетные документы. Раздел подготовки и акцепта актов оказанных услуг.
                    </li>
                </ul>
                <p>
                Операции.
                </p>
                <ul>
                    <li>
                    Платежные поручения
                    </li>
                    <li>
                    Поиск операций осуществляется по типу операции, соответствующей категории клиента.
                    </li>
                </ul>
                <p>
                Статистика.
                </p>
                <p>
                Раздел содержит аналитику и графики операций.
                </p>
                <ul>
                    <li>
                    Продажи.
                    </li>
                    <li>
                    Средний чек
                    </li>
                    <li>
                    Конверсия
                    </li>
                    <li>
                    Покупатели
                    </li>
                    <li>
                    Регулярные платежи
                    </li>
                </ul>
                <p>
                Колбэки.
                </p>
                <ul>
                    <li>
                    Содержит список колбэков (в очереди/отправленные)
                    </li>
                </ul>
                <p>
                Настройки: оповещения, адрес возврата, адрес проверки возможности оплаты.
                </p>




















            </AdaptiveDevBlock>
        </div>
    )
}

export default PersonalAccount