import React from 'react'

import styles from './CardRegistrationMFO.module.scss'
import img1 from '../../../img/developers/recurring/recurring-img.png'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import { Link } from 'react-router-dom'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const CardRegistrationMFO = (props) => {

    return (
        <div className={styles.CardRegistrationMFO}>
            <AdaptiveDevBlock>
            <h1>Регистрация карты (МФО)</h1>
				<p>
					 Описание и схема регистрации карты есть в разделе" <Link to="/developers/card-registration">Регистрация карты</Link>".
				</p>
				<p>
					 &nbsp;
				</p>
				<h2>Пример API запроса регистрации карты</h2>
				<p>
					 Домен для запросов - <a href="https://api.vepay.online">https://api.vepay.online</a>
				</p>
				<p>
					 1. Регистрация карты - отправляется запрос по API:
				</p>
				<p>
					 /card/reg
				</p>
				 <pre><code>
{`{
    "type": 1,
    "extid": "УФ-906/2187534.5e045b788e3c3",
    "timeout": 10,
    "successurl": "https://exemple.com/successurl",
    "failurl": "https://exemple.com/failurl",
    "cancelurl": "https://exemple.com/cancelurl"
}`}
</code>
</pre>
				<p>
					 Ответ:
				</p>
				 <pre><code>
{`{
"status": 1,
"message": "string",
"id": 101464,
"url": "https://test.vepay.online/pay/form/101464"
}`}
</code>
</pre>
				<p>
 <br/>
					 2.Клиент переходит на страницу тестового платежа (параметр url в ответе), где вводит данные карты, которая будет сохранена в системе.
				</p>
				<AdaptiveImg image={img1} />
				<p>
					 3. После завершения оплаты отправляется запрос по API на получение идентификатора карты:
				</p>
				<p>
					 /card/get
				</p>
				 <pre><code>
{`{
    "id": 101464,
    "type": 1
}`}
</code>
</pre>
				<p>
					 Ответ:
				</p>
				 <pre><code>
{`{
    "status": 1,
    "message": "string",
    "card": {
        "id": 356,
        "num": 540436******3366
        "exp": 10/23,
        "holder": Visa
    }
}`}
</code>
</pre>
				<p>
					 Данные карты необходимо сохранить, чтобы использовать их для следующих выплат.
				</p>
				<p>
					 Параметры запроса для регистрации карты (/card/reg):
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 type
					</td>
					<td>
						 Тип карты (0 – для автоплатежа и пополнения, 1 – только для пополнения)
					</td>
				</tr>
				<tr>
					<td>
						 extid
					</td>
					<td>
						 Внешний идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 timeout
					</td>
					<td>
						 Тайм-аут ожидания оплаты, от 10 до 59 минут
					</td>
				</tr>
				<tr>
					<td>
						 successurl
					</td>
					<td>
						 Адрес для Url для возврата после заверщения платежа (успех)
					</td>
				</tr>
				<tr>
					<td>
						 failurl
					</td>
					<td>
						 Адрес для Url для возврата после отказа от оплаты
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа для Регистрации карты (/card/reg):
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Статус (0 – ошибка 1 - успешно)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Сообщение ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 Идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 url
					</td>
					<td>
						 Cсылка для перехода на форму регистрации
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры запроса для получения идентификатора карты (/card/get)
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 Идентификатор запроса регистрации карты
					</td>
				</tr>
				<tr>
					<td>
						 type
					</td>
					<td>
						 Тип карты (0 – для автоплатежа, 1 – для пополнения)
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа для получения идентификатора карты (/card/get)
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Код ошибки (0 – ожидане оплаты 1 - успешно 2 - ошибка платежа)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Описание ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 Идентификатор карты
					</td>
				</tr>
				<tr>
					<td>
						 num
					</td>
					<td>
						 Номер карты
					</td>
				</tr>
				<tr>
					<td>
						 exp
					</td>
					<td>
						 Срок действия (мм/гг)
					</td>
				</tr>
				<tr>
					<td>
						 holder
					</td>
					<td>
						 Держатель карты
					</td>
				</tr>
				</tbody>
				</table>













            </AdaptiveDevBlock>
        </div>
    )
}

export default CardRegistrationMFO