import React from 'react'

import classes from './OpenMenuButton.module.scss'
import menuButton from '../../../img/openMenuButton.svg'
import { connect } from 'react-redux'
import { closeMenu, openMenu } from '../../../redux/actions/layoutActions'




const OpenMenuButton = ({ openMenu, closeMenu }) => {



    return (
        <img 
            className={classes.OpenMenuButton} 
            src={menuButton} 
            alt="menu-button"
            onClick={openMenu}
        />
    )
}




export default connect(null, { closeMenu, openMenu })(OpenMenuButton)

