import React from 'react'

import styles from './MySlider.module.scss'

import Slider from "react-slick";


const MySlider = ({className, children, sliderSettings, ...props}) => {

    const settings = sliderSettings || {
      dots: true,
      infinite: true,
      speed: 2000,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      autoplay: true,
      cssEase: "linear",
      autoplaySpeed: 5000,
      pauseOnHover: true,

      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        }
      ]
    };

    const cls = [
        styles.MySlider,
        className
    ]

    return (
        <div className={cls.join(' ')}>
            <Slider {...settings}>
            {
              children
            }
         
        </Slider>

        </div>
    )
}

export default MySlider