import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './CardPaymentMFO.module.scss'

const CardPaymentMFO = (props) => {

    return (
        <div className={styles.CardPaymentMFO}>
            <AdaptiveDevBlock>
            <h1>Выплата на карту (МФО)</h1>
				<p>
					 Данный платежный метод реализован для сервиса МФО (Микрофинансовая организация)
				</p>
				<p>
					 Выплата на карту - это выплата денежных средств на карту клиента при оформлении займа у микрофинансовой организации.
				</p>
				<p>
					 Сценарии выплаты на карту:
				</p>
				<ol>
					<li>Можно произвести выплату на зарегистрированную карту, указав при запросе идентификатор&nbsp;либо номер карты.</li>
					<li>Можно воспроизвести выплату на карту, которая не зарегистрирована в системе.</li>
				</ol>
				<p>
					 Форма для ввода карты формируется на стороне МФО.
				</p>
				<p>
					 Схему регистрации карты можно посмотреть в разделе Регистрация карты.
				</p>
				<h2>&nbsp;</h2>
				<h2>Пример API запроса выплаты на карту</h2>
				<p>
				</p>
				<p>
					 1. Отправляется перевод средств на карту заёмщика:&nbsp;<a href="https://api.vepay.online/mfo/out/paycard">https://api.vepay.online/mfo/out/paycard</a>:
				</p>
				 <pre><code>
{`{
	"card": 356,
	"cardnum": "5404362832378173",
	"amount": 100,
	"extid": "101464", 
	"document_id":"1a9a18a6-4355-4cb4",
	"fullname": "Ivanov Ivan Ivanovich" 
}`}
</code>
</pre>
				<p>
					 Ответ:
				</p>
				 <pre><code>
 {`{
	"status": 1,
	"message": "string",
	"id":101130
}`}
</code>
</pre>
				<p>
					 Параметры запроса:
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 card
					</td>
					<td>
						 Идентификатор карты
					</td>
				</tr>
				<tr>
					<td>
						 cardnum
					</td>
					<td>
						 Номер карты (16/18 цифр)
					</td>
				</tr>
				<tr>
					<td>
						 amount
					</td>
					<td>
						 Cумма платежа
					</td>
				</tr>
				<tr>
					<td>
						 extid
					</td>
					<td>
						 Внешний идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 document_id
					</td>
					<td>
						 Номер договора
					</td>
				</tr>
				<tr>
					<td>
						 fullname
					</td>
					<td>
						 ФИО клиента
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа:
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Статус (0 – ошибка 1 - принят)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Описание ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 Идентификатор запроса
					</td>
				</tr>
				</tbody>
				</table>
	
















            </AdaptiveDevBlock>
        </div>
    )
}

export default CardPaymentMFO