import { CLOSE_CONNECT_MODAL, FAILURE_CONNECT_MODAL, OPEN_CONNECT_MODAL, SUCCESS_CONNECT_MODAL } from "./actionTypes"

export const openModal = () => {
  return {
    type: OPEN_CONNECT_MODAL
  }
}

export const closeModal = () => {
  return {
    type: CLOSE_CONNECT_MODAL
  }
}

export const submitSuccess = () => {
  return {
    type: SUCCESS_CONNECT_MODAL
  }
}

export const submitFailure = (error) => {
  return {
    type: FAILURE_CONNECT_MODAL,
    error
  }
}
