import React from 'react'
import { withRouter } from 'react-router-dom'
import DropDown from '../UI/DropDown/DropDown'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'

import styles from './DevelopersSideMenu.module.scss'

const DevelopersSideMenu = (props) => {
  const path = props.match.path

  const mainInfo = [
    {
      title: 'Введение', 
      link: `${path}/introduction`
    },
    {
      title: 'Возможности', 
      link: `${path}/capabilities`
    },
    {
      title: 'Вызов платежной формы (виджета)', 
      link: `${path}/payment-form`
    },
    {
      title: 'Личный кабинет', 
      link: `${path}/personal-account`
    },
    {
      title: 'Регистрация карты', 
      link: `${path}/card-registration`
    },
    {
      title: 'Схема подключения клиента', 
      link: `${path}/client-connection`
    },
  ]

  const paymentModel = [
    {
      title: 'Выплата на карту (МФО)', 
      link: `${path}/card-payment-mfo`
    },
    {
      title: 'Выплата на счет (МФО)', 
      link: `${path}/account-payment-mfo`
    },
    {
      title: 'Платежный виджет', 
      link: `${path}/payment-widget`
    },
    {
      title: 'Рекарринг и периодические платежи', 
      link: `${path}/recurring-payments`
    },
    {
      title: 'Формирование счета (Мерчант)', 
      link: `${path}/invoice-forming`
    },
  ]

  const mfoLinks = [
    {
      title: 'Выплата на карту', 
      link: `${path}/card-payment-mfo`
    },
    {
      title: 'Выплата на счет', 
      link: `${path}/account-payment-mfo`
    },
    {
      title: 'Подключение автоплатежа', 
      link: `${path}/auto-payment`
    },
    {
      title: 'Регистрация карты (МФО)', 
      link: `${path}/card-registration-mfo`
    },
    {
      title: 'Схема "Погашение займа"', 
      link: `${path}/loan-repayment-scheme`
    },
  ]

  const merchantlist = [
    {
      title: 'Рекарринг', 
      link: `${path}/merchant-recurring`
    },
    {
      title: 'Схема "Оплата заказа"', 
      link: `${path}/order-payment-scheme`
    },
  ]
  const paymentMethods = [
    {
      title: 'Apple Pay', 
      link: `${path}/apple-pay`
    },
  ]

  const CMS = [
    {
      title: '1С-Битрикс', 
      link: `${path}/bitrix-module`
    },
    {
      title: 'OpenCart', 
      link: `${path}/opencart-module`
    },
    {
      title: 'Shop-Script', 
      link: `${path}/shopscript-module`
    },
    {
      title: 'WordPress', 
      link: `${path}/wordpress-module`
    },
    {
      title: 'CS-Cart', 
      link: `${path}/cscart-module`
    },
    {
      title: 'PrestaShop', 
      link: `${path}/prestashop-module`
    },
    {
      title: 'Magento 2', 
      link: `${path}/magento-module`
    },
  ]

  const navLinkCls = [
    styles.link,
    styles.mb1rem
  ].join(' ')

    return (
        <div className={styles.DevelopersSideMenu}>
            <DropDown
              title='Общая информация'
              links={mainInfo}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/connection`} className={navLinkCls}>
              API. Подключение
            </NavLinkPurple>
            <div>
               <hr/>
            </div>
            <DropDown
              title='Модель проведения платежей'
              links={paymentModel}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/splitting-payments`} className={navLinkCls}>
            Сервис Расщепление платежей
            </NavLinkPurple>
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/crowdfunding`} className={navLinkCls}>
            Сервис Краудфандинг
            </NavLinkPurple>
            <div>
               <hr/>
            </div>
            <DropDown
              title='Сервис МФО'
              links={mfoLinks}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <DropDown
              title='Сервис Мерчант'
              links={merchantlist}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/uprid`} className={navLinkCls}>
            Сервис УПРИД
            </NavLinkPurple>
            <div>
               <hr/>
            </div>
            <DropDown
              title='Платежные методы'
              links={paymentMethods}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <DropDown
              title='CMS модули'
              links={CMS}
              titleClass={styles.link}
              className={styles.mb1rem}
            />
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/sbp`} className={navLinkCls}>
            Система быстрых платежей
            </NavLinkPurple>
            <div>
               <hr/>
            </div>
            <NavLinkPurple to={`${path}/faq`} className={navLinkCls}>
            FAQ
            </NavLinkPurple>

        </div>
    )
}

export default withRouter(DevelopersSideMenu)