import React from 'react'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'

import styles from './Crowdfunding.module.scss'

const Crowdfunding = (props) => {

    return (
        <div className={styles.Crowdfunding}>
            <AdaptiveDevBlock>
            <h1>Сервис Краудфандинг</h1>
				<p>
					 Краудфандинг - это процесс сбора денежных стредств на реализацию определенного проекта. Бенефициар (получатель), заинтересованный в собирании денежных средств посредством краудфандинга, должен заключить договор с владельцем краудфандинговой платформы и представить ему необходимые документы.
				</p>
				<p>
					 Краудфандинговая платформа – специальный сайт в интернете. Платформа содержит информацию об условиях краудфандингового проекта, обеспечивает взаимодействие между его участниками, продвижение проекта, проведение платежей и предоставление участникам иного требуемого сервиса.
				</p>
				<p>
					 Участники краудфандинга:
				</p>
				<ul>
					<li>владелец краудфандинговой платформы - лицо, предоставляющее площадку (веб-сервис) бенефициару для сбора денежных средств.</li>
					<li>бенефициар - лицо, организующее сбор денежных средств для проекта. Бенефициаром могут выступать юридические и физические лица, а также индивидуальные предприниматели.</li>
					<li>инвестор - лицо, которое инвестирует денежные средства в проект.</li>
				</ul>
				<p>
					 Владелец номинального счета имеет право осуществлять любые платежные операции с денежными средствами на счете в интересах бенефициара и в соответствии с условиями, установленными договором номинального счета, в том числе перечислять средства за приобретаемые бенефициаром услуги.
				</p>
				<p>
					 &nbsp;
				</p>
				<h2>Описание Api Краудфандинг</h2>
				<p>
					 Swagger Api: <a href="https://api.vepay.online/kfapi#">https://api.vepay.online/kfapi#</a>
				</p>
				<p>
					 1. Осуществление перевода средств с карты инвестора на счет площадки, или возврат средств от заёмщика на счет площадки.
				</p>
				<p>
					 2. Перевод средств со счета площадки на карту заёмщика.
				</p>
				<p>
					 3. Перевод на счет юр.лица. Перевод средств со счета площадки на расчетный счет организации.
				</p>
				<p>
					 4. Перевод на счет физ.лица. Перевод средств со счета площадки на расчетный счет физ.лица.
				</p>
				<p>
					 5. Перевод средств со счета площадки на счет в банке.
				</p>
				<p>
					 6. Перечисление НДФЛ со счета площадки.
				</p>
				<p>
					 7. Запрос баланса счета площадки.
				</p>
				<p>
					 8. Запрос выписки по счету площадки.
				</p>
				<p>
 <br/>
					 Домен для запросов: <a href="https://api.vepay.online">https://api.vepay.online</a>
				</p>
				<p>
					 Пример API запроса на счет физ.лица:
				</p>
				 <pre><code>
/out/fl

{`
{
  "fio": "Ivanov Ivan Ivanovich",
  "account": "40817811538257753454",
  "bic": "044525234",
  "descript": "game project",
  "amount": 100,
  "extid": "101186",
  "sms": 1
}`}
</code>
</pre>
				<p>
					 Ответ
				</p>
				 <pre><code>
{`{
  "status": 1,
  "message": "",
  "id": 101525
}`}
</code>
</pre>
				<p>
					 Параметры запроса
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 fio
					</td>
					<td>
						 ФИО получателя
					</td>
				</tr>
				<tr>
					<td>
						 account
					</td>
					<td>
						 Счет в банке
					</td>
				</tr>
				<tr>
					<td>
						 bic
					</td>
					<td>
						 БИК банка
					</td>
				</tr>
				<tr>
					<td>
						 descript
					</td>
					<td>
						 Назначение платежа
					</td>
				</tr>
				<tr>
					<td>
						 amount
					</td>
					<td>
						 Cумма платежа
					</td>
				</tr>
				<tr>
					<td>
						 extid
					</td>
					<td>
						 Внешний идентификатор запроса
					</td>
				</tr>
				<tr>
					<td>
						 sms
					</td>
					<td>
						 Если передан 1, то требуется подтверждение кодом из смс
					</td>
				</tr>
				</tbody>
				</table>
				<p>
					 Параметры ответа:
				</p>
				<table>
				<tbody>
				<tr>
					<td>
						 Параметры
					</td>
					<td>
						 Значение
					</td>
				</tr>
				<tr>
					<td>
						 status
					</td>
					<td>
						 Статус (0 – ошибка 1 - принят)
					</td>
				</tr>
				<tr>
					<td>
						 message
					</td>
					<td>
						 Сообщение ошибки
					</td>
				</tr>
				<tr>
					<td>
						 id
					</td>
					<td>
						 идентификатор запроса
					</td>
				</tr>
				</tbody>
				</table>












            </AdaptiveDevBlock>
        </div>
    )
}

export default Crowdfunding