import React from 'react'

import styles from './PrestashopModule.module.scss'
import img1 from '../../../img/developers/CMS-modules/prestashop-module-img.jpg'
import img2 from '../../../img/developers/CMS-modules/prestashop-module-img2.jpg'
import img3 from '../../../img/developers/CMS-modules/prestashop-module-img3.jpg'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const PrestashopModule = (props) => {

    return (
        <div className={styles.PrestashopModule}>
            <AdaptiveDevBlock>
                <h1>PrestaShop модуль</h1>
                <h3>
                Инструкция по установке
                </h3>
                <p>
                1. Скачайте архив с модулем
                </p>
                <p>
                2. Откройте панель управления сайтом
                </p>
                <p>
                3. Перейдите в Модули {'>'} Module Manager и нажмите загрузить модуль, в открывшемся окне выберите скачанный архив
                </p>
                <AdaptiveImg image={img1} />
                <p>
                4. После загрузки модуля перейдите в настройки
                </p>
                <AdaptiveImg image={img2} />
                <p>
                5. Заполните поля и сохраните настройки
                </p>
                <AdaptiveImg image={img3} />






            </AdaptiveDevBlock>
        </div>
    )
}

export default PrestashopModule