import React from 'react'
import { getTrackBackground, Range } from 'react-range';

import styles from './InputRange.module.scss'

const InputRange = ({ STEP=0.1, MIN=0, MAX=10, units='', bgColor='#807CFA', values=[5], setValues}) => {
  // const [values, setValues] = useState([MAX / 2])
  
    return (
      <div className={styles.InputRange}>
        <Range
          values={values}
          step={STEP}
          min={MIN}
          max={MAX}
          onChange={setValues}
          renderTrack={({ props, children }) => (
            <div
              onMouseDown={props.onMouseDown}
              onTouchStart={props.onTouchStart}
              className={styles.inputRangeBlock}
              style={{
                ...props.style,
              }}
            >
              <div
                ref={props.ref}
                className={styles.rangeTrack}
                style={{
                  background: getTrackBackground({
                    values: values,
                    colors: [bgColor, '#ccc'],
                    min: MIN,
                    max: MAX
                  }),
                }}
              >
                <div className={styles.cyphers}>
                  <span> {'<'} {MIN} {units}  </span>
                  <span>{MAX} {units} {'<'}</span>
                </div>
                {children}
              </div>
            </div>
          )}
          renderThumb={({ props, isDragged }) => (
            <div
              {...props}
              className={styles.thumb}
              style={{
                ...props.style,
              }}
            >
              <div 
                className={styles.thumbTopText}
                style={{
                  backgroundColor: bgColor 
                }}
              >
                {values[0].toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ') + ' ' + units}
              </div>
              <div
                style={{
                  height: '16px',
                  width: '5px',
                  backgroundColor: isDragged ? bgColor : '#CCC'
                }}
              />
            </div>
          )}
        />
      </div>
    );
  
}

export default InputRange