import React from 'react'

import styles from './CardRegistration.module.scss'
import img1 from '../../../img/developers/card-registration/card-registration-img.png'
import AdaptiveDevBlock from '../../../components/AdaptiveDevBlock/AdaptiveDevBlock'
import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'
const CardRegistration = (props) => {

    return (
        <div className={styles.CardRegistration}>
            <AdaptiveDevBlock>
                <h1>Регистрация карты</h1>
                <p>
                Карта регистрируется для:
                </p>
                <ul>
                    <li>
                    проверки принадлежности ее клиенту;
                    </li>
                    <li>
                    осуществления переводов без указания реквизитов карты по внутреннему идентификатору карты в системе платёжного шлюза;
                    </li>
                    <li>
                    осуществления рекуррентных операций (операций безакцептного списания средств с карты(счёта) клиента)
                    </li>
                </ul>
                <p>
                Автоматическая генерация идентификатора карты происходит при проведении первой успешной оплаты или выплаты по банковской карте, а также при успешном холдировании средств.
                </p>
                <AdaptiveImg image={img1} />
                <h3>Схема регистрации карты:</h3>
                <p>
                1.Пользователь заходит на сайт продавца и выбирает услугу.
                </p>
                <p>
                2.Пользователь нажимает на кнопку "оплатить" и инициирует вызов платежного виджета.
                </p>
                <p>
                3.На стороне платёжной платформы выполняются подготовка виджета с учётом параметров вызова и передача к пользовательскому устройству данных для отображения.
                </p>
                <p>
                4.Платёжный виджет отображается в пользовательском интерфейсе. Пользователь вводит реквизиты карты для первой тестовой оплаты.
                </p>
                <p>
                5.Данные передаются в платёжную платформу через серверную часть веб-сервиса.
                </p>
                <p>
                6.На стороне платёжной платформы обрабатываются данные карты и данные платежа. Результат передается в платёжную среду.
                </p>
                <p>
                7.В платёжной среде открывается страница банка-эмитента. Пользователь подтверждает платёж с помощью кода из SMS. Нажимает "оплатить". Выполняются проверка карты и обработка платежа. После успешной проверки для карты формируется идентификатор. В платёжную платформу поступает информация о результате
                </p>
                <p>
                8.В платёжной платформе обрабатывается итоговая информация, после чего на заданный URL контрагента (если указан колбэк) отправляется программное оповещение о платеже.
                </p>
                <p>
                9.Происходит перенаправление пользователя на страницу клиента в соответствии с результатом оплаты.
                </p>










            </AdaptiveDevBlock>
        </div>
    )
}

export default CardRegistration